
/* Advance Search
--------------------------------------------------------*/
.advance-search{
  font-family: $RobotoFontFamily;
  position: relative;
  background-color: $white;
  border-bottom: 1px solid $border_color_two;
  .as-form-wrap{
    padding: 25px 30px 0 ;
  }
  .search-heading{
    background-color:  #394041;
    color: #dedee0;
    font-size: 21px;
    line-height: 48px;
    min-height: 53px;
    margin: 0;
    padding-right: 50px;
    position: absolute;
    top: -53px;
    left: 0;
    text-transform: capitalize;
    z-index: 999;

    .fa-search{
      padding: 13px 20px 15px;
      padding-bottom: 16px\9;
      margin-right: 20px;
      background-color:  #252a2b;
      font-size: 24px ;
    }
  }
  .advance-search-form{
    margin-left: -20px;
  }
  .option-bar{
    float: left;
    margin-left: 20px;
    &.large{
      width: 204px;
    }
    &.small{
      width: 148px;
    }
    input[type="text"]{
      box-sizing: border-box;
      width: 100%;
      height: 34px;
      @include placeholder( $textColor );

      &:focus{
        @include box-shadow( inset 0 0 3px 0 $border_color_two );
      }
    }
  }
  .more-option-trigger{
    margin: 10px 0 0 20px;
    i.fa{
      margin: 0 8px 0 0;
    }
  }
  .more-options-wrapper{
    &.collapsed{
      display: none;
    }
    .option-bar{
      height: auto;
      margin: 15px 0 0 20px;
    }
    label{
      margin: 0;
      display: inline-block;
      cursor: pointer;
      small{
        color: $linkColor;
      }
    }
    input[type="checkbox"]{
      margin: 0 5px 2px 0;
    }
  }
  label{
    color: $label_color;
    cursor: text;
    margin-bottom: 10px;
    span{
      color: lighten($label_color, 25%);
      font-size: 11px;
      line-height: 17px;
    }
  }
  .real-btn{
    font-size:  13px;
    margin: 30px 0 0;
    padding: 8px 28px;
    padding-top: 7px\9 ;
    padding-bottom: 7px\9 ;
    -webkit-padding-before: 7px  ;
    -webkit-padding-after:  7px  ;
  }
  .hide-fields{
    display: none;
  }
}

.page-template-template-search-php .advance-search{
  border-top: 1px solid #dedede;
}

@-moz-document url-prefix() {
  .advance-search .search-heading .fa-search{
    padding-bottom:16px;
  }
}

/* Select2 Styles
--------------------------------------------------------*/
.selectwrap {
  margin-bottom: 10px;
}

.select2-container {
  width: 100%;
  .select2-selection{
    font-family: $RobotoFontFamily;
    box-shadow: inset 0 0 2px #e9e9e9;
    border: 1px solid #cdcdcd;
    padding: 2px 0;
    height: 34px;
    line-height: 24px;
    color: $headingsColor;
    border-radius: 0;
  }
}

.select2-container--default .select2-selection--single {
  outline: none;
  .select2-selection__rendered{
    padding-right: 30px;
    padding-left: 6px;
    color: $headingsColor;
  }
  .select2-selection__arrow{
    height: 32px;
    width: 30px;
    background-color: #f7f7f7;
    border-style: solid;
    border-color: #dedede;
    border-top-width: 0;
    border-right-width: 0;
    border-bottom-width: 0;
    border-left-width: 1px;
    b{
      border-top-color: #cdcdcd;
    }
  }
}

.select2-container--default.select2-container--disabled .select2-selection--single{
  background-color: #f7f7f7;
}

.select2-container--default.select2-container--open{
  .select2-selection__arrow{
    b{
      border-bottom-color: #cdcdcd;
    }
  }
}

.select2-container--default.select2-container--focus{
  .select2-selection{
    @include box-shadow( inset 0 0 4px 0 $border_color_two );
  }
}

.select2-container .select2-dropdown{
  border-radius: 0;
  border-color: $border_color_two;
}

.select2-search--dropdown .select2-search__field{
  border-radius: 0;
  height: 30px;
}

.select2-container--default .select2-search--dropdown .select2-search__field{
  border-color: $border_color_two;
  margin-bottom: 0;
  &:focus{
    box-shadow: none;
  }
}

.select2-results__option{
  color: $headingsColor;
  &.select2-results__message{
    color: lighten( $headingsColor, 10% );
    font-size: 12px;
  }
}

.select2-container--default .select2-results__option[aria-selected=true]{
  background-color: #f7f7f7;
}

.select2-container--default .select2-results__option--highlighted[aria-selected]{
  background-color: $inspiryBlueBtnBG;
}

/**
* MultiSelect styles - Since version 3.0.2
*/
.select2-container--default.select2-container--focus .select2-selection--multiple {
  border-color: #cdcdcd;
  box-shadow: none;
  min-height: 34px;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice {
  background-color: #f7f7f7;
  margin-top: 3px;
}
