
/* Contact Page
--------------------------------------------------------*/
.contact-page{
  font-family: $RobotoFontFamily;
  .inner-wrapper{
    padding: 30px 25px;
  }

  .contact-form{
    padding: 0 7px;
  }

  #contact-form {
    #comment,
    input[type="text"]{
      font-size: 13px;
    }
  }

}

.contact-form #ajax-loader {
  display: none;
}

.contact-form input.error,
.contact-form textarea.error,
#overview .contact-form input[type="text"].error,
#overview .contact-form textarea.error {
  border: 1px solid #ff1a3a;
  box-shadow: inset 0 0 1px #ff1a3a;
}

#error-container{
  color: red;
  label{
    color: red;
  }
}

#message-container{
  color: $label_color;
}

#contact-form{
  #error-container {
    margin-bottom: 15px;
  }
}

.agent-detail #message-container,
.agent-detail #error-container{
  padding-left:0;
}

.agent-detail  label.error {
  color: #ff1a3a;
}

.map-container{
  margin-bottom: 25px;
}

#map_canvas {
  border: 1px solid $border_color_two;
  height: 418px;
  width:100%;
  iframe{
    height:100%;
    width: 100%;
    display: block;
  }
}

.contact-details {
  font-size: 13px;
  margin-bottom: 20px;
  padding: 0 23px;

  h3{
    font-size: 21px;
    margin: 0 0 12px;
  }

  address{
    .fa{
      font-size: 16px;
      margin-right: 10px;
    }
    float: left;
    line-height: 23px;
    padding-left:25px;
    width: 195px;
    border-left: none;
    padding-top: 0;
    background: transparent;
  }

  .contacts-list {
    margin-bottom: 20px;
    float:none;
    li{
      border: none;
      line-height: 20px;
      padding: 8px 0;
      svg{
        margin-right: 10px;
        margin-top: 1px;
      }
      a{
        color: inherit;
        &:hover{
          color: $linkColorHover;
        }
      }
    }
  }
}

.form-heading{
  @extend #comments-title;
  padding: 14px 22px 15px;
  margin: 0 0 26px;
}

.contact-form{
  #recaptcha_widget{
    .recaptchatable *{
      font-size: 6pt;
    }
    #recaptcha_response_field{
      font-size: 10pt;
    }
  }
}

