/**
 * Stripe Payment Button
 */

.property-payment {
	display: flex;
	flex-direction: column;
    justify-content: center;
	text-align: center;

	.stripe-button {
	    margin: 0 auto;
	    padding: 5px 0;

	    button.stripe-button-el {
	    	background: none;
		    border: none;
		    box-shadow: none;
		    background-color: $contact_number_bg_color;
		    border-radius: 0;
		    outline: 0;
		    color: $white;
		    transition: 0.2s background linear;

		    &:hover {
		    	background: darken( $contact_number_bg_color, 10% );

		    	span {
		    		background: darken( $contact_number_bg_color, 10% );
		    		transition: 0.2s background linear;
		    	}
		    }

		    &:focus {
		    	outline: 0;
		    	color: $white;

		    	span {
		    		color: $white;
		    	}
		    }

		    span {
		    	background: $contact_number_bg_color;
				text-shadow: none;
				box-shadow: none;
				border-radius: 0;
				font-weight: 400;
				transition: 0.2s background linear;
		    }
	    }
	}
}
