
/* widgets
--------------------------------------------------------*/
.widget{
  font-family: $RobotoFontFamily;
  font-size: 13px;
  line-height: 22px;
  background-color: #f2f2f2;
  margin-bottom: 28px;
  border: 1px solid $border_color_two;
  h3{ line-height: 20px;}

  .title{
    font-family: $RobotoFontFamily;
    font-size: 21px;
    line-height: 24px;
    font-weight: normal;
    margin:0 0 16px;
    padding: 15px 11px 16px 15px ;
    border-bottom: 1px solid $border_color_two;
    background-color: $white;
  }
  select{
    margin-bottom: 10px;
    width: auto;
    max-width: 100%;
  }
  ul{
    list-style: none;
    margin: 0;
    padding: 5px 18px 49px 22px;
    li{
      font-family: $RobotoFontFamily;
      color: #75797A;
      background: url(../../images/feature-list-bullet.png) no-repeat 2px 15px;
      border-bottom:  1px solid $border_color_two;
      padding: 8px 0 8px 14px;
      a{
        color: #75797A;
        &:hover{
          color:  $main_menu_background_color_2 ;
        }
      }
    }
    li:first-child{
      border-top:  1px solid $border_color_two;
    }
    ul.children{
      padding: 8px 0 0 0;
      li:last-child{
        border-bottom: 0;
        padding-bottom: 0;
      }
    }
  }
  ul.menu ul.sub-menu{
    padding-bottom: 0;
    padding-right: 0;
    padding-left: 0;

    li:last-child{
      border-bottom: 0;
      padding-bottom: 0;
    }
    .sub-menu .sub-menu .sub-menu .sub-menu {
      li {
        margin-left: -14px;
      }
    }
  }

  .textwidget{
    padding: 5px 18px 49px 22px;
    select{
      margin: auto;
    }
  }
  p{
    margin-bottom: 23px;
  }
  &.icl_languages_selector{
    border: none;
    background: transparent;
    #lang_sel{
      display: block;
      ul,
      ul ul{
        width: auto;
      }
      li{
        float: none;
      }
    }
    #lang_sel_click{
      ul li{
        background: none;
        border: none;
        padding: 0;
      }
    }
    #lang_sel_list{
      ul li{
        background: transparent;
        border: none;
        a{
          background: transparent;
          margin-right: 5px;
        }
        img.iclflag{
          top: 0;
        }
      }
    }
    #lang_sel_list.lang_sel_list_vertical{
      ul li{
        a{
          margin-right: 0;
        }
      }
    }

  }
  .agent-info{
    padding: 8px 25px 5px;
    color: $headingsColor;
    img{
      border: 1px solid $border_color_two;
    }
    .contacts-list{
      color: $headingsColor;
      margin-bottom: 15px;
      margin-top: 10px;
      padding: 0;
      list-style: none;
      li{
        background: none;
        padding: 10px 0 10px 5px;
        &:first-child{
          border-top: none;
        }
      }
    }
    p{
      margin: 0 0 15px;
    }
    .real-btn{
      margin-top: 15px;
    }
  }
  .enquiry-form{
    padding: 0 25px 10px;
    .agent-form-title{
      font-family: $RobotoFontFamily;
      font-weight: normal;
      font-size: 18px;
      margin: 10px 0 15px;
    }
    #agent-contact-form{
      margin: 0 0 10px;
    }
    #ajax-loader{
      display: none;
      margin: 0 0 0 10px;
    }
    input[type=text], textarea{
      width: 94%;
      margin-bottom: 15px;
    }
    textarea{
      min-height: 100px;
    }
    input[type="text"].error,
    textarea.error {
      border: 1px solid #ff1a3a;
      box-shadow: inset 0 0 1px #ff1a3a;
    }
    label.error{
      color: #ff1a3a;
      font-size: 13px;
    }
    #error-container{
      padding: 0;
    }
    .recaptcha_widget{
      margin-bottom: 15px;
    }
    .ajax-loader{
      margin: 0 10px 0;
      display: none;
    }
    .form-separator{
      margin: 0 0 15px;
    }
    .error-container{
      color: red;
    }
    .message-container{
      color: $headingsColor;
    }
  }
}


/* Search Widget
--------------------------------------------------------*/
.widget{
  #searchform{
    position: relative;
    width: 190px;
    margin: 0 auto 20px;
    label{
      display: none;
    }
  }
  #s{
    padding-right: 30px;
    width: 152px;
    margin-top: 20px;
    margin-bottom: 0;
  }
  #searchsubmit{
    position: absolute;
    background: url(../images/icon-lense.png) $white no-repeat center 7px;
    width: 30px;
    height: 30px;
    right: 2px;
    top: 22px;
    border: none;
    font-size: 0px;
    padding: 0;
    &:hover{
      background-position: center -25px ;
    }

  }
}


/* Tagcloud Widget
--------------------------------------------------------*/
.sidebar .widget .tagcloud{
  padding: 5px 18px 49px 22px;
}
.tagcloud a{
  display:block;
  float: left;
  padding: 6px 10px 7px 18px;
  position: relative;
  color: #fff;
  background-color: #4dc7ec;
  font-size: 0.78em;
  line-height: 1.2em;
  margin:0 1px 1px 0;
  text-decoration:none;
}
.tagcloud a:hover {
  background: $linkColorHover;
}
.tagcloud a:after {
  background: #fff;
  content: "";
  height: 4px;
  position: absolute;
  left: 7px;
  top: 11px;
  width: 4px;
  z-index: 999;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
}


/* Calendar Widget
--------------------------------------------------------*/
.widget #wp-calendar {
  width: 100%;
}
.widget #wp-calendar caption {
  color: #394041;
  //line-height: 44px;
  margin: 0 0 11px 0 ;
  text-align: left;
  font-size: 1.43em;

}
.widget #wp-calendar thead th {
  padding: 4px 8px;
  text-align: center;
  background-color: #4dc7ec;
  color: white;
  border: 1px solid white;
}
.widget #wp-calendar tbody {
  color: #656c6c;
}
.widget #wp-calendar tbody td {
  background: #ebebeb;
  border: 1px solid white;
  text-align: center;
  padding: 4px 8px;
}
.widget #wp-calendar tbody td:hover {
  background: #fff;
}
.widget #wp-calendar tbody td#today {
  background-color: #4dc7ec;
  color: white;
}
.widget #wp-calendar tbody td#today a {
  color: white;
}
.widget #wp-calendar tbody .pad {
  background: none;
}
.widget #wp-calendar tfoot td{
  text-transform: uppercase;
  background: transparent;
  border: 1px solid white;
  padding: 5px 10px;
}
.widget #wp-calendar tfoot #next {
  text-align: right;
}


/* Social Network Widget
--------------------------------------------------------*/
.widget.social_network{
  .title{
    margin-bottom: 0;
  }
  .social_networks {
    margin:  0 0 0 4px;
    padding: 0;
    float: none;
    li {
      border: none;
      background: none;
      padding: 0;
      &:first-child a{
        border-left: none;
      }
      a {
        width: 42.7px ;
        border: none;
        border-left: 1px solid #dedede;
        &:hover{
          color: $white;
        }
        i{
          width: auto;
          &.icon-twitter:before {
            left: 1px;
            position: relative;
            top: 0px;
          }
          &.icon-google-plus:before {
            left: 1px;
            position: relative;
            top: 0px;
          }
        }
      }
    }
  }
}


/*  Advance Search  Widget
--------------------------------------------------------*/
.widget.advance-search{
  background: $white;
  .title{
    background-color:  #394041;
    color: $white;
    font-size: 18px;
    line-height: 48px;
    position: static;
    padding:0 0 0 15px ;
    margin-bottom: 0;
    .fa-search {
      float: right;
      margin: 0 0 0 10px;
      color: #c7d2d3;
      padding-left: 15px ;
      padding-right: 15px;
    }
  }
  .as-form-wrap {
    padding: 18px 14px 18px;
  }
  .advance-search-form{
    margin: 0 0 0 -15px;
  }
  .option-bar{
    float: left;
    margin-left: 15px;
    height: auto;
    &.large{
      width: 93.9%;
    }
    &.small{
      width: 93.9%;
    }
  }
  label{
    margin-bottom: 5px;
  }
  .selectwrap input {
    color: #7b8283;
    margin-bottom: 10px;
  }
  ul li {
    background: none;
    margin-bottom: 0;
    padding-left: 10px;
    &:hover {
      background-color: #f7f7f7;
    }
  }
  .real-btn {
    margin: 6px 0 0;
    padding: 8px 30px;
  }
  .more-option-trigger {
    margin-left: 15px;
  }
  .more-options-wrapper {
    .option-bar {
      margin: 10px 0 0 15px;
      float: none;
    }
    label{
      margin-bottom: 0;
    }
    input[type="checkbox"]{
      margin: 0 5px 4px 0;
    }
  }
}


/* Mortgage Calculator Widget - Using https://wordpress.org/plugins/responsive-mortgage-calculator/ plugin
--------------------------------------------------------*/
.widget.widget_lidd_mc_widget{
  padding-bottom: 20px;
}

.lidd_mc_input_responsive input[type="text"]{
  box-sizing: inherit;
  height: inherit;
}

.lidd_mc_form{
  padding: 0 6% 10px 7%;
  margin-bottom: 0;
  .lidd_mc_input {
    margin-bottom: 0;
  }
  label {
    color: $headingsColor;
    margin-bottom: 5px;
  }
  input[type="number"],
  input[type="text"]{
    display: inline-block;
    width: 90%;
    border: 1px solid #cdcdcd;
    color: #394041;
    background-color: #fff;
    padding: 4px 6px;
    margin-bottom: 15px;
  }
  .lidd_mc_select_fancy_dark,
  .lidd_mc_select_fancy_light{
    width: 96%;
  }
  .lidd_mc_select_fancy_dark{
    border: 1px solid #cdcdcd;
    color: #394041;
    background-color: #fff;
  }
  select {
    margin: 0 0 15px 0;
  }
  .lidd_mc_select_fancy_dark select{
    color: #394041;
  }
  .lidd_mc_select_fancy_dark select,
  .lidd_mc_select_fancy_light  select{
    width: 100%;
    max-width: 110%;
    margin: -6px 0 0;
    padding: 0;
    display: block;
  }
  .lidd_mc_select:after{
    top: 1em;
  }
  .lidd_mc_error{
    color: red;
  }
  .lidd_mc_select_fancy_dark:after {
    border-top: .4em solid #394041;
  }
}

#lidd_mc_details{
  padding: 0 6% 0 7%;
  margin-top: 0;
  #lidd_mc_results{
    margin-bottom: 10px;
    p{
      margin-bottom: 10px;
    }
  }
  #lidd_mc_inspector {
    right: 7%;
    top: 2px;
    cursor: pointer;
  }
  #lidd_mc_summary{
    color: #7b8283;
    p{
      margin-bottom: 15px;
    }
  }
}


/* Calculator Widget
--------------------------------------------------------*/
.widget.calculator{
  .title{
    background: url(../images/icon-calculator.png) $white no-repeat 180px 9px ;
    margin-bottom: 0;
    padding-left: 15px;
  }
  input[type="text"] {
    width: 176px;
    height: 24px;
    margin-bottom: 14px;
    color: $form_input_color;
  }
  .as-form-wrap {
    padding: 17px 14px 0;
  }
  .selectwrap input {
    background-color: $white;
    color: #7b8283;
    margin-bottom: 14px;
  }
  ul li {
    background: none;
    margin-bottom: 0;
    padding-left: 10px;
    &:hover {
      background-color: #f7f7f7;
    }
  }
  .option-bar {
    float: left;
  }
  #interest{ background: url(../images/dropdown-interest.png) $white no-repeat right top ; }
  #insurance,
  #texes{ background: url(../images/dropdown-year.png) $white no-repeat right top ;}
  #extra,
  #pmi{ background: url(../images/dropdown-month.png) $white no-repeat right top ; }
  .mini{
    input {
      width: 150px;
      padding-right: 32px;
    }
  }
  #payment{
    width: 172px;
    padding-right: 10px;
  }
  #extra{
    width: 150px;
    padding-right: 32px;
  }
  .real-btn {
    float: left;
    padding: 8px 21px;
  }
}

.widget.advance-search,
.widget.calculator{
  .selectbox-wrapper{
    ul {
      padding: 0;
      li{
        padding: 6px 8px;
      }
    }
  }
}


/* Featured Properties Widget
--------------------------------------------------------*/
#footer .widget ul.featured-properties,
ul.featured-properties{
  padding: 0 15px;
  li{
    background: none;
    border: none;
    margin-bottom: 0;
    padding: 0 0 40px 0;
    &:last-child{
      padding-bottom: 25px;
    }
    &:first-child{
      border-top: none;
    }
    figure{
      margin: 0 0 16px;
      position: relative;

      span.property-label {
        position: absolute;
        font-size: 13px;
        padding: 2px 9px;

        left: 0px;
        right: auto;
        top: 0px;
        bottom: auto;
        color: $white;
        font-weight: normal;
      }
    }
    h4{
      margin: 0 0 7px;
    }
    h4, h4 a {
      font-size: 14px;
      font-family: $RobotoFontFamily;
      font-weight: normal;
      color: $headingsColor;
      &:hover{
        color: $linkColorHover;
      }
    }
    p{
      margin-bottom: 15px;
    }
    .price{
      font-size: 14px;
      font-weight: bold;
      color: $contact_number_bg_color ;
    }
  }
}
#footer .widget ul.featured-properties{
  padding: 0;
}


/* Mortgage Calculator Widget Styles - https://wordpress.org/plugins/mortgage-calculator/
--------------------------------------------------------*/
.widget .mc-wrapper{
  padding: 0 25px 10px;
  color: $headingsColor;

  input[type="number"],
  input[type="tel"],
  input[type="url"],
  input[type="email"],
  input[type="text"],
  input[type="password"] {
    height: 34px;
    margin-bottom: 10px;
  }

  p{
    margin-bottom: 10px;
  }

  label.error{
    margin-top: 0;
  }

  input[type = "submit"]{
    padding: 8px 16px;
  }

}

#footer .widget .mc-wrapper{
  padding: 0 0 10px;
}

/* Quick and Easy Tweets Widget Styles - https://wordpress.org/plugins/quick-and-easy-tweets/
--------------------------------------------------------*/
.widget .qaet-tweets {
  padding: 15px 25px 30px;

  .qaet-tweet {
    position: relative;
    padding-left: 50px;

    &:before {
      position: absolute;
      top: 6px;
      left: 0;
      content: "\f099";
      display: inline-block;
      width: 36px;
      height: 36px;
      line-height: 38px;
      text-align: center;
      font-family: 'FontAwesome';
      font-size: 1.4rem;
      background-color: #85cdff;
      color: #fff;
    }
  }

  .qaet-tweet:not(:last-child) {
    margin-bottom: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid $border_color_two;
  }

  p {
    margin-bottom: 0;
  }

  small {
    display: block;
  }
}

#footer .widget .qaet-tweets {
  padding: 0;

  .qaet-tweet {
    padding-left: 0;

    &:before {
      display: none;
    }
  }

  .qaet-tweet:not(:last-child) {
    margin-bottom: 15px;
    padding-bottom: 10px;
    border-bottom: none;
  }

  small{
    &:before {
      content: "-";
      display: inline-block;
    }
  }

}
