/*
  Testimonials Styles
*/

.qe-testimonial-wrapper{

  .qe-testimonial-img{
    margin: 10px 0;
    img{
      border: 5px solid #eee;
      &:hover{
        border-color: #dedede;
      }
    }
  }

  .qe-testimonial-name{
    margin: 10px 0;
    color: $headingsColor;
  }

  .qe-testimonial-byline{
    margin: 8px 0;
  }

  .qe-testimonial-text{
    position: relative;
    margin:10px 15px 15px;
    padding-top: 15px;
    &:before{
      content: "";
      position: absolute;
      left: 40%;
      top: 0;
      width: 20%;
      height: 1px;
      background-color: $headingsColor;
    }
  }

}