
/* Blog Page
--------------------------------------------------------*/
.page-template-template-home-php .inner-wrapper{
  border-top: none;
  margin-bottom: 0;
  .hentry{
    margin-bottom: 0;
  }
}
.inner-wrapper{
  padding: 16px 20px 25px;
  border-top: 1px solid $border_color_two;
  border-bottom: 1px solid $border_color_two;
  background-color:  $white;
  margin-bottom: 52px;

  figure,
  .post-video {
    position: relative;
    margin: 0 0 32px;
  }


  .hentry {
    margin-bottom: 15px;
    overflow: hidden;
    color: #75797a;
    p {
      line-height: 27px;
      margin-bottom: 20px;
    }
    p.wp-caption-text{
      margin-bottom: 0;
      padding-bottom: 0;
    }
    strong{
      color: #252626;
    }
    li{
      line-height: 27px;
    }
    a.real-btn{
      margin: 0px 0 30px;
    }
    &.sticky{
      background: url(../images/sticky-pin.png) no-repeat right 62px;
    }
  }

  .pagination {
    margin: -21px -20px 15px;
    padding: 22px 20px 0;
    border-top: 1px solid $border_color_two;
    a{
      margin-left: 2px;
    }
  }

}

p.wp-caption-text{
  margin-bottom: 0;
  padding-bottom: 0;
}

.post-title{
  font-size: 27px;
  line-height: 36px;
  font-family: $RobotoFontFamily;
  font-weight: normal;
  color: $headingsColor;
  word-spacing: -1px;
}

.post-title a {
  @extend .post-title;
  &:hover{
    color: $linkColorHover;
  }
}

.post-title{
  margin: 0 0 17px;
}

.post-meta {
  font-family: $RobotoFontFamily;
  border-top: 1px solid $border_color_two;
  border-bottom: 5px solid $contact_number_bg_color;
  color: $headingsColor;
  padding: 16px 0 23px;
  line-height: 18px;
  span{
    color: $headingsColor;
  }
  a{
    color: $main_menu_background_color;
    &:hover{
      color: $headingsColor;
    }
  }
//  .date{
//    color:  #a4a4a4 ;
//  }
  span.author-link a{
    color: $headingsColor;
    &:hover{
      color: $main_menu_background_color;
    }
  }
}

.inner-wrapper .hentry .post-meta{
  display: block;
}

.post-meta.thumb-not-exist{
  // border-bottom: none;
}

.post-meta.format-video{
  border-bottom: 5px solid $contact_number_bg_color;
}

.format-icon{
  display: block;
  width: 50px;
  height: 44px;
  position: absolute;
  top: -49px;
  right: 0;
}

.format-image .format-icon.image{
  background: url(../../images/icon-image.png) $contact_number_bg_color no-repeat center center;
}

.format-video .format-icon.video{
  background: url(../../images/icon-video.png) $contact_number_bg_color no-repeat center center;
}

.format-gallery .format-icon.gallery{
  background: url(../../images/icon-gallery.png) $contact_number_bg_color no-repeat center center;
}

.format-video{
  a{
    position: relative;
    &:hover .play-btn {
      display: block;
    }
  }
  .play-btn {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -51px;
    margin-left: -51px;
    height: 102px;
    width: 102px;
    background: url(../../images/play-btn.png)  no-repeat center center;
  }
}


.post-video {
  .video-wrapper {
    width: 100%;
    position: relative;
    padding: 0;
    padding-top: 40%;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 0;
    }
  }
}

// Gallery Slider
.listing-slider {
  position: relative;
  margin: 0 0 32px;

  .slides, .flex-control-nav, .flex-direction-nav {
    list-style: none ;
    margin: 0;
    padding: 0;
  }

  .flex-control-paging {
    bottom: 5px;
    left: 0;
    li a {
      background: rgba(77, 199, 236,1);
      box-shadow: none;
      height: 11px;
      width: 11px;
      z-index: 1600;
      cursor: default;
      &:hover,
      &.flex-active {
        background: $white;
      }
    }
  }

  .flex-direction-nav a {
    height: 50px;
    top: 50%;
    width: 32px;
    opacity: 1;
    text-indent: -999em;
    text-align: left;
    transition: none;
    text-shadow: none;
    outline: none;
    &::before{
      display: none;
    }
    &.flex-next {
      background: url(../../images/flex-arrow-right.png) no-repeat center center;
      background-color: rgba(77, 199, 236,0.9);
      background-color: rgb(77, 199, 236)\9;
      right: 0;
    }
    &.flex-prev {
      background: url(../../images/flex-arrow-left.png)  no-repeat center center;
      background-color: rgba(77, 199, 236,0.9);
      background-color: rgb(77, 199, 236)\9;
      left: 0;
    }
    &:hover{
      background-color: rgba(77, 199, 236,1);
    }
    &:focus{
      outline: 0;
    }
  }

}


/* Blog Single
--------------------------------------------------------*/
.single {
  article {
    p {
      margin-bottom: 15px;
      color: $textColor;
    }
    blockquote{
      p{
        line-height: 24px
      }
    }
    ul, ol{
      margin: 10px 0 20px 40px;
      ul, ol{
        margin-bottom: 0;
      }
    }
  }
}

article {
  h1, h2, h3, h4, h5, h6 {
    span{
      color: #ec894d;
    }
  }
}

.common-list{
  padding: 12px 0 0 ;
  list-style: none;
  margin-bottom: 30px;
  font-family: $baseFontFamily;
  li{
    background: url(../../images/feature-list-bullet.png) no-repeat left 7px;
    margin-bottom: 5px;
    padding: 0 0 0 26px;
  }
}

.disc-list ul{
  @extend .common-list;
  li{
    background: url(../../images/icon-disc-small.png) no-repeat 4px 9px;
  }
}

.arrow-list ul{
  @extend  .common-list;
  li{
    background: url(../../images/icon-arrow.png) no-repeat 0px 7px;
  }
}

.small-arrow-list ul{
  @extend  .common-list;
  li{
    background: url(../../images/icon-arrow-small.png) no-repeat 4px 8px;
  }
}

.tick-list ul{
  @extend  .common-list;
  li{
    background: url(../../images/icon-tick.png) no-repeat left 4px;
  }
}


blockquote{
  color: #383838;
  border-left: 7px solid #dedede;
  background-color:#ededed ;
  padding: 24px 20px 13px 30px;
  margin : 0  32px 30px 38px  ;
  font-size: 14px;
  p{
    font-size: 14px;
    line-height: 24px
  }
}

address{
  color: #383838;
  border-left: 7px solid #dedede;
  background-color:#ededed ;
  padding: 20px 20px 15px 25px;
  margin : 0  20px 30px 0;
  font-size: 14px;
  line-height: 24px;
}


/* Comments
--------------------------------------------------------*/
#comments-title{
  border-top: 2px solid $contact_number_bg_color;
  background-color: #ededed;
  font-family: $RobotoFontFamily;
  font-size: 21px ;
  font-weight: normal;
  margin: 0;
  padding: 14px 14px 15px;
}

#comments {
  padding-top: 15px;
  margin-bottom: 0px;
  a{
    color: $headingsColor;
    &:hover{
      color:  $main_menu_background_color;
    }
  }

  .commentlist {
    margin: 0;
    padding: 2px 0px 5px 0px;
    overflow: hidden;
    .children {
      margin: 0;
      padding: 0;
      .comment-detail-wrap {
        width: 83%;
        .comment-meta p {
          margin-bottom: 2px;
        }
      }
    }
  }

  .comment {
    list-style: none;
    margin: 20px 0 0;
    padding: 8px 0 2px;
    > article {
      margin: 0;
      overflow: hidden;
    }
    .avatar {
      border: 1px solid $border_color_two;
      padding: 5px;
      background-color: #ededed;
      float: left;
      margin: 0 0 10px 0;
    }
    .author {
      font-size: 14px;
      color: $headingsColor;
      margin: 0 0 5px;
      a {
        margin: 0 ;
      }
      cite {
        color: #4c595c;
        font-style: normal;
      }
      .comment-reply-link {
        font-size: 11px;
      }
    }
    time {
      margin: 1px 0 5px;
      color:  #a4a4a4;
    }
  }
}

.commentlist li.pingback{
  margin-left: 0;
  list-style: none;
  border-bottom: 1px solid #dedede;
  padding: 10px;
  p{
    margin: 0;
  }
}

.comment-detail-wrap {
  border: 1px solid $border_color_two;
  float: right;
  padding: 10px 25px 15px 25px;
  position: relative;
  width: 71%;
  .comment-body p,
  .comment-meta p{
    line-height: 24px;
  }
  .comment-meta p{
    margin-bottom: 9px;
    color: $headingsColor;
  }
  .comment-body p{
    margin-bottom: 12px;
    color: #8B9293;
  }
  .comment-detail-wrap-arrow{
    display: block;
    position: absolute;
    left: -14px;
    top: 8px;
    width:14px;
    height: 29px;
    background: url(../../images/comment-detail-wrap-arrow.png) no-repeat 0 0 ;
  }
}


#comments .commentlist .children .depth-2 .comment-detail-wrap{
  width: 71%;
}
#comments .commentlist .children .depth-3 .comment-detail-wrap{
  width: 71%;
}
#comments .commentlist .children .depth-4 .comment-detail-wrap{
  width: 71%;
}
#comments .commentlist .children .depth-5 .comment-detail-wrap{
  width: 71%;
}

.bypostauthor .comment-detail-wrap{
  border-top-width: 3px;
  border-bottom-width: 3px;
}

#contact-form,
#respond {
  font-family: $RobotoFontFamily;
  margin-top: 25px;

  #reply-title{
    @extend #comments-title;
  }

  #commentform{
    padding: 30px 15px 0;
    margin: 0 0 0;
  }

  p {
    margin-bottom: 25px;
  }

  label{
    color: $headingsColor ;
    margin-bottom: 4px;
  }

  input[type="text"],
  #comment{
    width: 40.4%;
    height: 24px;
    margin-bottom: 0px;
  }

  #comment{
    width: 64%;
    height: 143px;
    margin-bottom: 0px;
    resize: none;
  }

  p.form-allowed-tags{
    font-size: 12px;
    code{
      font-size: 12px;
      display: block;
    }
  }

}

