/*	Submit Property Template
--------------------------------------------------------*/
.submit-form{
  .form-option{
    margin-bottom: 15px;
    &.checkbox-option{
      input[type="checkbox"]{
        display: block;
        width: 6%;
        float: left;
        margin-top: 3px;
      }
      label{
        display: block;
        width: 94%;
        float: left;
        font-weight: normal;
      }
    }
    &.terms-field-wrapper {

      input[type="checkbox"] {
        width: 5%;
      }

      label {
        width: 95%;
        float: right;
        margin-top: -1px;
      }
    }
  }

  label{
    color: $headingsColor;
    font-size: 14px;
    font-weight: bold;
    &.error{
      color: red;
      font-size: 12px;
      font-weight: normal;
    }
    small{
      font-weight: normal;
    }
  }
  input[type="file"] {
    line-height: 15px;
  }
  input[type="text"],
  input[type="email"],
  input[type="password"],
  textarea{
    width: 424px;
    &.error{
      border: 1px solid red;
    }
  }
  .selectwrap input[type="text"]{
    width: 400px;
  }
  textarea{
    height: auto;
  }

  .map-wrapper img{
    max-width: none;
  }

  .ui-helper-hidden-accessible{
    display: none;
  }

  .features-checkboxes{
    list-style: none;
    margin: 8px 0 15px;
    li{
      margin: 0 0 8px 0;
      float: left;
      width: 33%;
    }
    input[type="checkbox"]{
      margin: 2px 0 0px 0;
      display: block;
      float: left;
      width: 10%;
    }
    label{
      display: block;
      margin:0 0 0 5px;
      color: $textColor;
      font-size: 12px;
      font-weight: normal;
      width: 85%;
      float: left;
    }
  }

  .field-description{
    font-size: 12px;
  }

  .map-wrapper{
    padding-bottom: 10px;
    .real-btn{
      margin: 5px 0 15px;
    }
    .map-canvas{
      width: 100%;
      height: 352px;
      margin-bottom: 10px;
    }
  }

  .image{
    margin-bottom: 6px;
  }

  #add-more{
    margin-bottom: 10px;
  }

  #featured-file-container{

  }

  #featured-file-container.hidden{
    display: none;
  }

  #featured-thumb-container,

  #gallery-thumbs-container{
    margin: 0 0 10px -8px;
  }

  .gallery-thumb,
  .sortable-placeholder{
    width: 141px;
    height: 141px;
    float: left;
    margin: 0 0 8px 8px;
    background-color: #efefef;
  }

  .gallery-thumb{
    position: relative;
    display: table;
    text-align: center;
    background-color: #efefef;
    -webkit-box-shadow: inset 0px 0px 0px 2px rgba(223,223,223,1);
    -moz-box-shadow: inset 0px 0px 0px 2px rgba(223,223,223,1);
    box-shadow: inset 0px 0px 0px 2px rgba(223,223,223,1);

    span{
      display: table-cell;
      vertical-align: middle;
      text-align: center;
      font-weight: bold;
    }
    img{
      width: 141px;
      height: 141px;
      position: absolute;
      top: 0;
      left: 0;
    }
    .mark-featured,
    .loader,
    a{
      display: block;
      background-color: rgba(0,0,0,0.5);
      color: #f0f0f0;
      padding: 5px 8px;
      position: absolute;
      right: 0;
      bottom: 0;
      font-size: 16px;
      &:hover{
        color: #fff;
        background-color: rgba(0,0,0,0.6);
      }
    }
    .loader{
      right: auto;
      bottom: auto;
      left: 0;
      top: 0;
      background-color: rgba(0,0,0,0.6);
      color: #fff;
      display: none;
    }
    .mark-featured{
      right: auto;
      top: auto;
      bottom: 0;
      left: 0;
    }
  }

  /* Additional details
  --------------------------------------------------------*/
  .inspiry-detail {
    margin: 5px 0;

    .inspiry-detail-control{
      width: 5%;
      float: left;
      color: #aaa;
    }

    .inspiry-detail-title{
      width: 33%;
      float: left;
    }

    .inspiry-detail-value{
      width: 57%;
      float: left;
    }

    label{
      display: block;
      font-weight: normal;
    }

    input[type="text"]{
      width: 96%;
      height: 34px;
      margin: 0;
      -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
      -moz-box-sizing: border-box;    /* Firefox, other Gecko */
      box-sizing: border-box;         /* Opera/IE 8+ */
    }

  }

  .detail-placeholder{
    margin: 5px 0;
    background: #f9f9f9;
    height: 34px;
  }

  .sort-detail{
    cursor: move;
  }

  .sort-detail:hover{
    color: #666;
  }

  .inspiry-details-wrapper a{
    color: #aaa;
  }

  .inspiry-details-wrapper a:hover,
  .inspiry-details-wrapper a:focus{
    color: #666;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: none;
  }

  .inspiry-details-wrapper .fa{
    text-decoration: none;
    position: relative;
    top: 7px;
    font-size: 16px;
  }

}


.form-options-container{
  margin-left: -18px;
  .form-option{
    float: left;
    width: 210px;
    margin-left: 18px;
  }
  input[type="text"]{
    width: 196px;
    margin-bottom: 12px;
  }
  .selectwrap input[type="text"]{
    width: 172px;
  }
  .full{
    float: none;
    width: 439px;
    input[type="text"]{
      width: 423px;
    }
    .selectwrap input[type="text"]{
      width: 199px;
    }
  }
}
input[type="text"].ui-autocomplete-loading {
  background: transparent url('../images/loading.gif') no-repeat right center;
  visibility: visible;
}
.ui-autocomplete-input.open {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.ui-widget {
  font-family: $RobotoFontFamily;
}
.ui-autocomplete {
  padding: 0;
  margin: 0;
  list-style: none;
  position: absolute;
  z-index: 10000;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  border: 1px solid #ccc;
  background: #fff;
  color: $headingsColor;

  .ui-state-focus,
  .ui-state-hover {
    border: none;
    cursor: pointer;
    font-weight: 400;
  }
}
.ui-autocomplete li {
  margin-bottom: 0;
  white-space: nowrap;
  text-align: left;
  color: $headingsColor;

  &:focus {
    outline: none;
  }
}
.ui-autocomplete li a {
  display: block;
  height: 100%;
  padding: 4px 10px;
}
.ui-autocomplete li a.ui-state-focus {
  cursor: pointer;
}
.form-option .agent-options{
  label{
    display: inline-block;
    font-weight: normal;
    font-size: 13px;
  }
  input[type="radio"]{
    margin-top: -1px;
  }
  select{
    width: auto;
    margin: 0 0 0 5px;
    border-radius: 0;
  }
}
#drag-and-drop {
  margin-bottom: 10px;
  background-color: #efefef;
  border: 3px dashed #dfdfdf;
}
.drag-drop-msg {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 15px;
  font-size: 18px;
}
.drag-or {
  text-align: center;
}
.drag-btn {
  text-align: center;
  margin-bottom: 30px;
  margin-top: 15px;
}
#errors-log {
  color: red;
}
#submit-property-form {
  #wp-description-editor-tools {
    .wp-editor-tabs {
      .wp-switch-editor {
        margin: 0 0 0 5px;
      }
    }
  }
  #wp-description-wrap {
    .wp-editor-container{
      border: 1px solid #e5e5e5;
    }
  }
}
