.SFOI{
  width: 100%;
  padding: 50px 0;
  min-height: 475px;

  &__content{
    width: 820px;
    margin: 50px auto 0;
    color: $headingsColor;
  }

  &__title{
    font-size: 48px;
    line-height: 58px;
    text-transform: capitalize;
    color: white;
    text-align: center;
    margin-bottom: 20px;
    text-shadow: 0 0 3px #252a2b;
  }

  &__description{
    font-size: 20px;
    font-weight: 300;
    line-height: 26px;
    color: $headingsColor;
    text-align: center;
    margin-bottom: 25px;
    text-transform: capitalize;
  }

  &__form-wrapper{
    margin-top: 30px;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.2);
    position: relative;
  }

  &__form{
    margin: 0;
  }

  &__top-fields-wrapper{
    background: #ffffff;
    padding: 23px 25px 20px;
  }

  &__top-fields-container{
    margin: 0 0 0 -20px;
    overflow: hidden;
    .option-bar{
      width: 200px;
      &:last-child{
        width: auto;
      }
    }
  }

  &__advanced-fields-wrapper{
    background: #f6f6f6;
    padding: 23px 25px 20px;
    display: none;
  }

  &__advanced-fields-container{
    margin: 0 0 0 -20px;
    overflow: hidden;
    .option-bar{
      width: 175px;
    }
  }

  .option-bar{
    float: left;
    margin-left: 20px;
  }

  input[type="text"]{
    box-sizing: border-box;
    width: 100%;
    height: 34px;
  }

  input[type="text"],
  input[type="number"],
  input[type="search"]{
    -webkit-appearance: inherit;
  }

  input[type="submit"]{
    margin-top: 25px;
    padding: 7px 33px;
  }

  .hide-fields{
    display: none;
  }

  &__advanced-expander{
    padding: 4px 5px 4px 10px;
    position: absolute;
    background: #252a2b;
    right: 0;
    bottom: -28px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    line-height: 20px;
    cursor: pointer;
    i{
      margin: 0 5px 0 6px;
    }
    &:hover{
      background: lighten( #252A2B , 8% );
    }
  }

  .more-option-trigger{
    margin: 10px 0 0 20px;
    i{
      margin-right: 3px;
    }
  }

  .more-options-wrapper{
    .option-bar{
      width: auto;
      margin: 10px 0 0 20px;
    }
    input[type="checkbox"]{
      margin: 0 3px 1px 0;
    }
    label{
      font-size: 13px;
      display: inline-block;
      margin: 0;
    }
    &.collapsed{
      display: none;
    }
  }
}

@media (min-width: 1200px) {
  .SFOI{
    min-height: 500px;
  }
}

@media (min-width: 768px) and (max-width: 979px) {
  .SFOI{
    min-height: 400px;

    &__content{
      width: 700px;
      margin-top: 0;
      margin-top: 0 !important;
    }
    &__title{
      font-size: 32px;
      line-height: 42px;
      margin-bottom: 15px;
    }
    &__description{
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 15px;
    }
    &__top-fields-container{
      .option-bar{
        width: 170px;
        &:last-child{
          width: auto;
        }
      }
    }
    &__advanced-fields-container{
      .option-bar{
        width: 147px;
      }
    }
    input[type="submit"]{
      padding: 7px 20px;
    }
  }
}

@media (max-width: 767px) {
  .SFOI{
    min-height: auto;
    padding: 50px 0 100px;
    &__title{
      font-size: 32px;
      line-height: 42px;
      margin-bottom: 15px;
    }
    &__description{
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 15px;
    }
    &__content{
      width: 100%;
      margin-top: 0;
      margin-top: 0 !important;
    }
    &__top-fields-container{
      .option-bar{
        width: 25%;
      }
    }
    &__advanced-fields-container{
      .option-bar{
        width: 22%;
      }
    }
    input[type="submit"]{
      padding: 7px 20px;
    }
  }
}


@media (max-width: 700px) {
  .SFOI{
    &__top-fields-container{
      .option-bar{
        width: 24%;
      }
    }
    &__advanced-fields-container{
      .option-bar{
        width: 21%;
      }
    }
  }
}


@media (max-width: 610px) {
  .SFOI{
    &__top-fields-container{
      .option-bar{
        width: 45%;
        &:last-child{
          width: 45%;
        }
      }
    }
    &__advanced-fields-container{
      .option-bar{
        width: 29%;
      }
    }
    input[type="submit"]{
      padding: 7px 50px;
      width: 100%;
    }
  }
}


@media (max-width: 499px) {
  .SFOI{
    &__advanced-fields-container{
      .option-bar{
        width: 45%;
      }
    }
  }
}


@media (max-width: 439px) {
  .SFOI{
    &__top-fields-container{
      .option-bar{
        width: 93%;
        &:last-child{
          width: 93%;
        }
      }
    }
    &__advanced-fields-container{
      .option-bar{
        width: 93%;
      }
    }
    input[type="submit"]{
      margin-top: 10px;
    }
  }
}