/*	Print Styles
--------------------------------------------------------*/
@media print {

  * {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }

  a,
  a:visited {
    text-decoration: underline;
  }

  a[href]:after {
    content: "";
  }

  abbr[title]:after {
    content: " (" attr(title) ")";
  }

  [class*="span"]{
    width: auto;
  }

  /*
   * Don't show links for images, or javascript/internal links
   */
  .ir a:after,
  a[href^="javascript:"]:after,
  a[href^="#"]:after {
    content: "";
  }

  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }

  thead {
    display: table-header-group; /* h5bp.com/t */
  }

  tr,
  img {
    page-break-inside: avoid;
  }

  img {
    max-width: 100% !important;
  }

  #property_map img{
    max-width: none !important;
  }

  @page {
    margin: 0.5cm;
  }

  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }

  h2,
  h3 {
    page-break-after: avoid;
  }

  /*
  * Theme specific css
  */
  body .container,
  .page .container,
  .single .container{
    width: auto;
    margin-bottom: 0;
  }

  .page .inner-wrapper,
  .single .inner-wrapper{
    padding: 0;
    border: none;
    margin-bottom: 0;
  }

  .only-for-print{
    display: block;
  }

  .only-for-print.inline{
    display: inline;
  }

  .only-for-print.inline-block{
    display: inline-block;
  }

  /* Header Top */
  #header-top{
    margin-bottom: 0;
    border: none;
    text-align: left;

    #contact-email{
      display: inline;
      i{
        display: none;
      }
    }
  }

  body.sticky-header{
    padding-top: 0 !important;
  }
  .header-wrapper.stick{
    position: relative !important;
    padding-top: 0 !important;
  }
  .sticky-header #property-featured-image{
    margin-top: 20px;
  }

  /* Logo */
  #logo{
    img{
      display: none;
    }
    h2.logo-heading{
      margin: 0;
    }
  }

  .contents .main-wrap,
  .contents .sidebar-wrap{

  }

  /* Header */
  #header{
    .contact-number{
      margin-top: 0;
      .fa-phone{
        margin: 0;
        position: relative;
        top: 3px;
        padding: 10px;
      }
    }
    .tag-line{
      text-align: left;
      margin: 0;
      span{
        margin: 0;
        padding: 0;
      }
    }
  }

  span9.main-wrap{
    width: 100%;
  }

  .main{
    padding-top: 10px;
    border: none;
  }

  /* Post Detail Page */
  .page,
  .single-post {
    .hentry{
      .post-title, .post-title a{
        margin: 0 0 5px;
      }
      .post-meta{
        border: none;
        padding: 0 0 10px;
      }
      img, pre, blockquote {
        page-break-inside: avoid;
      }
    }
  }


  /* Property Detail Page */
  #property-slider-two-wrapper,
  #property-detail-flexslider{
    display: none;
  }

  .detail #overview {
    .property-item{
      .wrap{
        margin-bottom: 5px;
        page-break-inside: avoid;
      }
      .title{
        padding: 0;
        float: none;
        margin: 0;
        font-weight: bold;
      }
      .price{
        float: none;
        padding: 0;
        margin: 0 0 5px 0;
        font-size: 14px;
        span {
          padding: 0;
          margin: 0;
          display: inline-block;
        }
        .status-label{
          padding: 0 10px 0 0;
        }
      }
      .property-meta{
        border: none;
        margin: 0 0 10px 0;
        span{
          border: none;
          margin-left: 0;
          padding: 0 15px 0 0;
          float: left;
          i{
            display: none;
          }
        }
        .add-to-fav{
          display: none;
        }
      }
      .content {
        padding: 0;
        margin-bottom: 0;
      }
      .features{
        border: none;
        margin-top: 25px;
        page-break-inside: avoid;
        ul.arrow-bullet-list{
          margin: 0px;
          li{
            width: 200px;
            margin: 0 0 0 20px;
            padding: 0;
            float: left;
            list-style-type: circle;
            background-image: none !important;

            &:before {
              content: none;
            }
          }
        }
        h4{
          border: none;
          padding: 0px;
          margin-bottom: 15px;
        }
        .arrow-bullet-list li{
          background: url(../../images/feature-list-bullet.png) no-repeat 10px 14px !important;
        }
      }
    }
    .map-wrap {
      padding: 0px;
      border: none;

      .map-label{
        display: none;
      }
      #property_map{
        width: auto;
        page-break-inside: avoid;
      }
    }
    .agent-detail{
      padding: 0;
      margin: 20px 0 0;
      page-break-inside: avoid;
      .left-box{
        width: auto;
        float: none;
        figure img{
          border: none;
        }
        .contacts-list{
          float: none;
          width: auto;
          border: none;
          li {
            border: none;
            margin: 0 0 10px 0;
            padding: 0;
          }
        }
        .real-btn {
          display: none;
        }
      }
      .contact-form{
        display: none;
      }
    }
  }

  /* Single Agent */
  .single-agent{
    .listing-layout {
      border: none;
      .about-agent{
        border: none;
        padding: 0;
        margin: 0;
        border-bottom: 1px solid #dedede;
        margin-bottom: 20px;
      }
    }
  }

  .listing-grid-layout .title-heading{
    padding: 0;
    position: static;
    top: auto;
    left: auto;
    border-top: 1px solid #dedede;
  }
  .listing-grid-layout .main{
    margin: 20px 0 0;
    padding: 0;
  }

  .listing-layout {
    border: none;
    .list-container{
      border-top: 1px solid #dedede;
      padding: 10px 0 0;
      .property-item{
        padding: 0;
        border: none;
        page-break-inside: avoid;
        figure{
          margin: 0 10px 10px 0;
          figcaption{
            background: #fff !important;
            color: #000 !important;
          }
        }
        .detail{
          width: auto;
          float: none;
        }
        .property-meta{
          margin: 0;
          padding: 0;
          border: 1px solid #dedede;
          span{
            padding: 5px 10px;
            i{
              display: none;
            }
          }
        }
        .more-details{
          display: none;
        }
      }
    }
  }

  .home{
    .narrative{
      padding: 10px;
    }
    .property-items{
      .span6{
        width: auto;
      }
      .property-item{
        h4, h4 a{
          margin-bottom: 10px;
        }
        page-break-inside: avoid;
        figure{
          margin: 0 10px 10px 0;
          figcaption{
            background: #fff !important;
            color: #000 !important;
          }
        }
        .detail{
          width: auto;
          float: none;
        }
        .property-meta{
          span{
            padding: 5px 10px;
            i{
              display: none;
            }
          }
        }
      }
    }
  }

  // common note
  .common-note{
    page-break-inside: avoid;
  }

  // property attachments
  #overview .attachments-wrap{
    padding: 25px 0px;
  }

  // common
  #currency-switcher,
  #header-top #lang_sel,
  .user-nav,
  .advance-search,
  .pagination,
  .view-type,
  .follow-agent,
  .detail .listing-layout.property-grid,
  .page-head,
  .social_networks,
  .main-menu,
  .property-item .printer-icon,
  .property-item .printer-icon a,
  .property-video,
  .share-networks,
  .sidebar,
  #comments,
  .featured-properties-carousel,
  #footer-wrapper,
  .brands-carousel,
  .slider-socket,
  .property-views,
  .inspiry-post-nav {
    display: none !important;
  }

  #scroll-top{
    display: none !important;
  }

  .switcher__lang {
    display: none;
  }

  .single-property {
    .property-sidebar {
      .widget {
        display: none;
      }
    }
  }

  .single-property {
    .property-sidebar {
      display: block;

      .property-agent {
        display: block;
        border: none;
        border-bottom: 1px solid #dedede;
        padding-bottom: 20px;

        .property-agent-title {
          border-bottom: none;
          padding: 15px 0;
          margin: 0;
        }
        .agent-info {
          padding: 0;

          a {
            display: inline-block;
            float: left;
          }

          .contacts-list {
            display: inline-block;
            vertical-align: top;
            float: left;
            margin: 0 auto;
            padding: 0 0 0 30px;

            li {
              border-top: none;
              border-bottom: none;
              padding: 0 0 10px 0;
            }
          }
          p {
            display: inline-block;
            vertical-align: top;
            float: left;
            width: 450px;
            padding: 0 0 0 30px;

            .real-btn {
              display: none;
            }
          }
        }

        .enquiry-form {
          display: none;
        }
      }
    }
  }

}
