/**
 * Memberships Styles
 *
 */

#ims_select_membership {
	.form-option {
		padding: 0 0 15px;

		h4 {
			margin-top: 0;
		}
	}
}

.ims-membership_loader {
	height: 32px;
	padding: 0;
    float: right;

	img {
		display: none;
	}
}

#ims_recurring_label {
	display: inline-block;
	margin: 4px 0 0 0;
    vertical-align: middle;
}

.ims-button-option {
	padding: 0 0 15px;

	#ims-free-button {
		display: inline-block;
		background: $main_menu_background_color;
		border: none;
		color: $white;
		padding: 10px 20px;
		transition: 0.2s background linear;

		&:hover {
			background: darken( $main_menu_background_color, 10% );
			transition: 0.2s background linear;
		}
	}
}

.ims-paypal-button {
	a {
		display: inline-block;
		padding: 0;
	}
}

.ims-receipt-button {
	a {
		display: inline-block;
		background: $main_menu_background_color;
		color: $white;
		padding: 10px 20px;
		transition: 0.2s background linear;

		&:hover {
			background: darken( $main_menu_background_color, 10% );
			transition: 0.2s background linear;
		}
	}
}

.ims-stripe-button {
	a {
		display: inline-block;
		background: $main_menu_background_color;
		color: $white;
		padding: 10px 20px;
		transition: 0.2s background linear;

		&:hover {
			background: #00afe1;
			transition: 0.2s background linear;
		}
	}

	button {
		background: none;
		border-radius: 0;
		box-shadow: none;
		outline: 0;
		text-shadow: none;

		&:focus {
			outline: 0;
			background: none;

			span {
				background: #00afe1 !important;
				border: none !important;
				outline: 0 !important;
			}
		}

		span {
			background: $main_menu_background_color;
			border-radius: 0;
			box-shadow: none;
			font-weight: normal;
			padding: 5px 20px;
			text-shadow: none;
			transition: 0.2s background linear;

			&:hover {
				background: #00afe1;
				transition: 0.2s background linear;
			}
		}
	}
}

.ims-btn {
	border: none;
    padding: 5px 12px;
    color: $white;
    background: $main_menu_background_color;
    outline: none;
    margin: 0 5px 0 0;
    transition: background 0.2s linear;

    &:hover {
		background: darken( $main_menu_background_color, 10% );
		transition: background 0.2s linear;
	}
}

#rh-cancel-modal {
	.modal-header {
		display: flex;
		justify-content: space-between;

		button {
			order: 2;
		}
	}
}
