/**
 * Optima Express Styles
 *
 */

#post-0 {
	overflow: visible;
}

.inner-wrapper {
	.optima-express {
		overflow: visible;
	}
}

#home-idx-search {
	.form-inline {
		margin: 20px 25px;
	}
}

#ihf-main-container {
	color: $label_color;

	.row {
		& > .row {
			margin-left: 0;
			margin-right: 0;
		}
	}

	.modal-content {
		border-radius: 0;
	}

	.ihf-one-selectedArea {
		margin-bottom: 4px;
	}

	a {
		color: $label_color;
		text-decoration: none;

		&:hover {
			color: $linkColorHover;
			text-decoration: none;
		}
	}

	.btn {
		border-radius: 0;
		text-shadow: none;
	}

	.btn-primary {
		background: $main_menu_background_color;
		border-color: #fff;
		border-radius: 0;
		text-shadow: none;
		padding: 10px;
		transition: 0.2s all linear;

		&:hover,
		&:active,
		&:focus {
			background: $main_menu_background_color_2;
			border-color: #fff;
			box-shadow: none;
			transition: 0.2s all linear;
		}
	}

	.btn-primary.disabled {
		border: none;
		color: #333;
	}

	.btn.btn-default {
		background: $main_menu_background_color;
		color: $white;
		border-radius: 0;
		box-shadow: none;
		border-color: $main_menu_background_color;
		font-size: 14px;
		text-shadow: none;
		padding: 10px 15px;
		transition: 0.2s all linear;

		&:hover,
		&:active,
		&:focus {
			background: $main_menu_background_color_2;
			border-color: $main_menu_background_color_2;
			box-shadow: none;
			transition: 0.2s all linear;
		}
	}

	.btn-primary.active {
		background: $main_menu_background_color_2;
		border-color: #fff;
		background-image: none;
		box-shadow: none;
	}

	.btn.dropdown-toggle {
		background: #fff;
		color: $label_color;
		text-decoration: none;
	    border: 1px solid $border_color_two;
	    font-size: 14px;
	    padding: 5px 10px 5px 10px;
	    box-shadow: none;
	    margin-left: 5px;
	    margin-top: 5px;
	    cursor: pointer;
	    transition: 0.2s all linear;

	    &:hover,
	    &:focus {
	    	background: #fff;
	    	color: $label_color;
	    	border: 1px solid $border_color_two;
	    	box-shadow: none;
	    	transition: 0.2s all linear;
	    }

	    .caret {
	    	margin-top: 0;
			margin-left: 10px;
	    }
	}

	.dropdown-menu {
		border-radius: 0;
		box-shadow: none;

		li {
			a {
				&:hover {
					background: $contact_number_bg_color;
				}
			}
		}

		li.active {
			a {
				background: $contact_number_bg_color;
			}
		}
	}

	#ihf-sort-search-form {
		display: none;
	}

	.ihf-slider-col,
	.ihf-grid-result {
		.ihf-grid-result-container {
			display: flex;
			flex-direction: column;
			box-shadow: none;
			border-radius: 0;

			.row {
				&:nth-child(1) {
					order: 2;
					margin-bottom: -20px;
				}
			}

			.ihf-grid-result-price {
				border-radius: 0;
				border: none;
				background: $contact_number_bg_color;
				padding: 10px 20px;
				margin-bottom: 0;
				margin-top: 5px;

				a {
					font-weight: normal;
				}
			}

			div.col-xs-10 {
				a {
					display: block;
					width: 100%;
				}
			}

			.ihf-grid-result-address {
			    display: block;
			    width: 100%;
			    padding: 0 0 5px 0;
			    font: normal 18px/27px "Roboto", Helvetica, Arial, sans-serif;
			    color: $label_color;
			    text-transform: capitalize;
			    white-space: normal;

			    &:hover {
			    	color: $linkColorHover;
			    }
			}

			.ihf-grid-result-basic-info-item1 {
				padding-right: 15px;
			}

			.ihf-grid-result-basic-info-item2 {
				padding: 0 15px;
			}

			.ihf-grid-result-basic-info-item3 {
				padding-left: 15px;
			}

			.ihf-grid-result-photocount {
				display: flex;
				justify-content: flex-end;
				align-items: center;
				padding-top: 10px;
    			padding-bottom: 10px;

				a {
					display: inline-block;
				    vertical-align: top;
				    line-height: 11px;
				    margin-right: 5px;
				    color: $label_color;

				    &:hover {
				    	color: $linkColorHover;
				    }

				    i {
				    	font-size: 16px;
						line-height: 12px;
				    }
				}

				span {
				    display: inline-block;
				    vertical-align: top;
					font-size: 14px;
					color: $label_color;
				}
			}

			.mb-5 {
				display: none;
			}

			.property-divider {
				margin: 20px 0 0 0;
			}

			.ihf-grid-result-mlsnum-proptype {
				font-size: 14px;
				line-height: 21px;
				color: $label_color;
				text-align: center;
				margin: 15px 0 0 0;
				white-space: normal;
			}
		}
	}

	.ihf-listing-search-results {

		.btn {
			color: $label_color;
			padding: 0;
			text-decoration: none;

			&:hover {
				color: $linkColorHover;
				text-decoration: none;
			}
		}

		.btn.disabled {
			padding: 10px 15px;
			border: none;
		}

		.btn-group {

			.btn:not(.dropdown-toggle) {
				border-radius: 0;
				color: $white;
				background: $main_menu_background_color;
				border-color: $main_menu_background_color;
				box-shadow: none;
				padding: 7px 10px;
				text-shadow: none;

				&:hover {
					background: $main_menu_background_color_2;
					border-color: $main_menu_background_color_2;
				}
			}

			.btn.active:not(.dropdown-toggle) {
				background: $main_menu_background_color_2;
				border-color: $main_menu_background_color_2;
			}

			.btn-group+.btn-group {
				margin-left: 0;
			}
		}

		#saveSearchButton {
			color: $white;
			background: $main_menu_background_color;
			border-radius: 0;
			border: none;
			box-shadow: none;
			padding: 7px 12px;
			text-shadow: none;

			&:hover {
				background: $main_menu_background_color_2;
				color: $white;

			}
		}

		.dropdown-menu {
			border-radius: 0;
			box-shadow: none;

			li {
				a {
					&:hover {
						background: $contact_number_bg_color;
					}
				}
			}

			li.active {
				a {
					background: $contact_number_bg_color;
				}
			}
		}

		#ihf-refine-search {

			.form-control {
				border-radius: 0 !important;
				box-shadow: none;
				width: 100%;
			}

			.ihf-widget-label {
				display: block;
			}

			#ihf-mini-search-maxprice,
			#ihf-mini-search-minprice,
			#ihf-mini-search-beds,
			#ihf-mini-search-baths {
				width: 100%;
			}
		}

		& > .mb-10 {

			.col-xs-4 {
				padding-top: 6px;
    			padding-bottom: 6px;
			}

			.dropdown-toggle {
				color: $label_color;
				text-decoration: none;
			    border: 1px solid $border_color_two;
			    font-size: 14px;
			    padding: 5px 10px 5px 10px;
			    box-shadow: none;
			    margin-left: 5px;
			    margin-top: 5px;

			    &:hover {
			    	color: $label_color;
			    	border: 1px solid $border_color_two;
			    }

			    .caret {
			    	margin-top: 0;
    				margin-left: 10px;
			    }
			}
		}

	}

	div.modal {
		top: 300px;
	    left: 280px;
	    width: 100%;
	    background: none;
	    border: none;
	}

	span.carousel-control.left,
	span.carousel-control.right {
		background: none;
	    border: none;
	    top: 45%;
	    opacity: 0.8;
	    transition: 0.1s all linear;

	    &:hover {
	    	opacity: 1;
	    	transition: 0.1s all linear;
	    }

	    span {
	    	color: #292d2d;
	    	text-shadow: 0px 0px 5px rgba(255, 255, 255, 1);
	    }
	}

	div.carousel-caption {
		background: none;
    	padding: 20px 10px;
	}

	div.modal-header {
		border-radius: 0;
	}

	div.modal-footer {
		border-radius: 0;

		.btn-link {
			border: none;
			outline: 0;
			text-decoration: none;
			color: $white;
			background: $main_menu_background_color;
			box-shadow: none;

			&:hover {
				background: $main_menu_background_color_2;
			}
		}
	}

	ul.pagination {
		border: none;
		padding: 0;

		li.disabled {
			a {
				cursor: text;

				&:hover {
					cursor: text;
				}
			}
		}

		li.disabled,
		li {
			a {
				color: $label_color;
				border-color: $border_color_two;
				border-radius: 0;

				&:hover {
					color: $label_color;
				}
			}
		}
		li {
			display: inline-block;
			vertical-align: top;
			margin-right: 5px;

			&:first-child,
			&:last-child {
				a,
				span {
					color: $white;
					border: 1px solid $main_menu_background_color;
					background: $main_menu_background_color;
					border-radius: 0;
					margin: 0;

					&:hover {
						color: $white;
						background: $main_menu_background_color_2;
						border: 1px solid $main_menu_background_color_2;
					}
				}
			}
		}
	}

	.ihf-board-update-text {
		font-size: 14px;
	}

	.back-to-results {
		a {
			display: inline-block;
		    vertical-align: top;
		    text-decoration: none;
		    color: $label_color;

		    &:hover {
		    	color: $linkColorHover;
		    }
		}
	}

	#ihf-leadcapture-btns {
		a.schedule-showing-btn,
		a.info-req-btn,
		a.save-listing-btn {
			background: $main_menu_background_color;
			border: none;
			border-radius: 0;
			padding: 10px;

			&:hover {
				background: $main_menu_background_color_2;
			}
		}
	}

	h4.ihf-address {
		line-height: 1.5;
	}

	h4.ihf-price {
		color: $main_menu_background_color;
		line-height: 1.5;
	}

	div.ihf-social-share {

		.ihf-share-btn-facebook,
		.ihf-share-btn-email,
		.ihf-share-btn-print,
		.ihf-share-btn-more {
			background: $main_menu_background_color;
		    padding: 7px !important;
		    margin: 0;
		    border: none;
		    border-radius: 0;
		    width: 30px;
		    height: 30px;

		    &:hover,
		    &:focus {
		    	background: $main_menu_background_color_2;
		    }
		}

		.btn-group.open {
			.dropdown-toggle {
				box-shadow: none;
			}

			ul.dropdown-menu {
				border-radius: 0;
				box-shadow: none;

				li {
					a {
						box-shadow: none;

						&:hover {
							background: $main_menu_background_color;
							color: $white;

							span.btn {
								color: $white !important;
							}
						}

						span.btn {
							color: $label_color !important;
							background: none;
							font-size: 14px !important;
    						box-shadow: none;
						}
					}
				}
			}
		}
	}

	.extra-link {
		margin: 0;

		a {
			display: inline-block;
			padding: 7px 15px;
			text-decoration: none;
			color: $white;
			background: $main_menu_background_color;

			&:hover {
				color: $white;
				background: $main_menu_background_color_2;
			}
		}
	}

	.ihf-description {
		line-height: 28px;
	}

	#ihf-detail-extrainfo-tabrow {
		ul {
			li {
				a {
				    border-radius: 0;
					color: $label_color;
					font-weight: 700;

					.caret {
						vertical-align: top;
						border-top-color: $label_color;
						border-bottom-color: $label_color;
					}
				}
			}
		}
		.nav {
			.open {
				a {
					&:focus {
						border-color: #ddd;
						border-bottom-color: transparent;
					}
				}
			}
		}
	}

	.ihf-tab-pane {
	    font-size: 14px;
	}

	.ihf-detail-tab-content {
		overflow: visible;

		#ihf-detail-features-tab {

			h5 {
				font-weight: 700;
			}

			.row {
				margin-left: -20px;
				margin-right: -20px;
			}

			.title-bar-1 {
				color: $label_color;
			    padding: 10px 20px;
			    background-color: #f8f8f8;
			    font-size: 14px;
			    font-weight: 700;
			    border: none;
			    border-radius: 0;
			    border-top: 1px solid #dedede;
			    border-bottom: 1px solid #dedede;
			}

			.listing-info-item {
				padding: 10px 0;
			    margin: 0 5px;
			    font-size: 14px;
			}
		}
	}

	#ihf-mortgage-calculator-tab-content {
		.well {
			box-shadow: none;
			border-radius: 0;

			.fs-12 {
				font-size: 14px;
			}

			.input-group {
				.form-control {
					box-shadow: none;
					border-radius: 0 !important;
					border-color: #cccccc;
					height: 35px;
				}
				.input-group-addon {
					border-radius: 0;
				}
			}

			#ihf-mort-calc-submit {
				outline: 0;
				border-radius: 0;
				background: $main_menu_background_color;
				color: $white;
				box-shadow: none;
    			border: 0;
			}

			.mc-loanamt {
				margin-top: 10px;
			}

			.panel {
				border-color: #e3e3e3;
				border-radius: 0;

				.mc-amount,
				.mc-lineitem {
					color: $label_color;
				}

				.mc-estimated {
					text-decoration: none;
				}
			}
		}
	}

	#ihf-agent-sellers-rep,
	#ihf-board-detail-disclaimer,
	#ihf-board-disclaimers,
	#ihf-board-detail-updatetext,
	#ihf-board-update-text {
		font-size: 14px;
	}

	.ihf-widget {
		padding: 0;
		margin-bottom: 15px;

		.row {
			.field-label {
				font-size: 14px;
			}

			.form-control {
				outline: 0;
				border-radius: 0 !important;
				box-shadow: none;

				&:focus {
					border-color: #cccccc;
				}
			}

			button {
			    border-radius: 0;
				box-shadow: none;
				background: $main_menu_background_color;
				border-color: $main_menu_background_color;
				font-size: 14px;
				outline: 0;
				text-shadow: none;
				padding: 6px 12px;

				&:hover {
					background: $main_menu_background_color_2;
				}
			}

			.input-group-btn {
				padding: 0 0 0 5px;
			}
		}

		#ihf-widget-contact-form-replace,
		.ihf-more-info-widget-container {
			margin-bottom: 25px;
		}
	}

	.row.mb-10 {
		margin-bottom: 10px;
	}

	.gallery-prop-info {
		padding: 0;
		margin-bottom: 15px;
		font-size: 14px;

		.address {
			a {
				display: inline-block;
				padding: 0 0 10px 0;
				text-decoration: none;
				color: $label_color;

				&:hover {
					color: $linkColorHover;
				}
			}
		}

		div.col-xs-6 {
			width: 50%;

			&:first-child {
				width: 50%;
				padding-right: 0;
			}
		}

		div.gallery-sidebar-photo {
			margin-bottom: 10px;
		}
	}

	.chosen-container-single {

		.chosen-single {
		    border-radius: 0;
		    box-shadow: none;
		    font-size: 14px;

		    span {
		    	color: $label_color;
		    }
		}

		.chosen-drop {
		    box-shadow: none;
		    border-radius: 0 !important;
		    border-color: #ccc;
		}

		.chosen-results {
			margin-right: 0;
			padding: 0;

			li {
				color: $label_color;
			    background-image: none;
			    background-color: $white;

			    &:hover {
			    	color: $white;
			    	background-color: $main_menu_background_color;
			    }
			}

			li.highlighted {
				background-color: $main_menu_background_color;
			}

			li.result-selected {
				color: $white;
				background-color: $main_menu_background_color;
			}
		}
	}

	.checkbox input[type=checkbox],
	.checkbox-inline input[type=checkbox],
	.radio input[type=radio],
	.radio-inline input[type=radio] {
		display: inline-block;
		margin-right: 7px;
		margin-bottom: 2px;
		float: none;
	}

	.radio+.radio {
		padding-left: 0;

		label {
			padding-left: 20px;
		}
	}

}

.widget.widget_ihomefinderpropertiesgallery {

	& > a {
		display: block;
	    margin: 0 15px 25px;
	    text-align: center;
	    padding: 10px 0;
	    background: $main_menu_background_color;
	    color: $white;
	    font-size: 14px;

	    &:hover {
	    	background: $main_menu_background_color_2;
	    }
	}

	br {
		display: none;
	}
}

.widget.widget_ihomefinderagentbiowidget {
	background: $white;

	table {
		background-color: transparent;
	    border: none;
	    border-color: transparent;
	    margin: 0;

	    .ihf-bio-img {
	    	padding: 0 15px 15px 15px;
		    border-color: transparent;
		    background-color: $white;
		    border-bottom: 1px solid #dedede;

		    img {
		    	display: block;
		    	margin: 0 auto;
		    }
	    }

	    td {
	    	border: none;
	    	padding: 15px;
	    }

	    .ihf-bio-about-info {
	    	strong,
	    	span {
	    		color: $label_color !important;
	    		font-family: Helvetica, Arial, sans-serif !important;
	    	}

	    	div {
	    		color: $label_color;
	    		font: 14px/1.5 Helvetica, Arial, sans-serif;
	    	}
	    }
	}
}

.widget.widget_ihomefinderlinkwidget {
	padding: 15px;

	.ihf-seo-link {
		display: inline-block;
		vertical-align: top;
	}

	a {
		font-size: 14px;
		line-height: 2;
		padding: 0 5px 0 0;
	}
}

.widget.widget_ihomefindersocialwidget {
	border: none;

	a {
		display: inline-block;
		vertical-align: middle;
		margin-top: 5px;
	}
}

.widget.widget_ihomefinderloginwidget {
	.dropdown {
		margin: 0 0 15px;

		ul li {
			padding: 0;
			background: none;
			border: none;
		}
	}

	#ihf-main-container .btn.dropdown-toggle {
		margin: 0;
	}

	#ihf-main-container {
		.dropdown-menu {
			& > li > a {
				padding: 7px 20px;
				transition: 0.1s all linear;

				&:hover {
					transition: 0.1s all linear;
				}
			}

			.divider {
				margin: 5px 0;
			}
		}
	}
}

.widget {
	#ihf-main-container {
		display: block !important;
		padding: 0 15px;
	}
}

#ihf-main-search-form {

	.nav-tabs {
		li {
			a {
				border-radius: 0;
				color: $label_color;
			}
		}
	}

	.tab-content,
	#areaPickerContainer {
		overflow: visible;

		.input-group {
			.input-group-addon {
				border-radius: 0;

				.areaPickerExpandAllButtonClass {
					&:first-child {
						display: none !important;
					}
				}

				.glyphicon {
					padding: 0px 2px 2px 2px;
				}
			}
			.form-control {
				border-radius: 0;
				box-shadow: none;

				input {
					padding: 0 !important;
				}
			}
		}
	}

	.areaPickerExpandAllTopBar {
		border-radius: 0;
		background: $main_menu_background_color;
		border-color: $main_menu_background_color;
		color: $white;
		text-shadow: none;
		box-shadow: none;
		padding: 0;

		&:hover {
			background: $main_menu_background_color_2;
			border-color: $main_menu_background_color_2;
		}

		.glyphicon {
			color: $white !important;
		}

		.areaPickerExpandAllButtonClass {
			span {
				background: none;
			}
		}

	}

	#areaPickerExpandAllContainer {
		border-radius: 0;
		font-size: 14px;
		box-shadow: none;

		.areaPickerExpandAllElement {
			font: 14px/2 Helvetica, Arial, sans-serif;
			color: $label_color;
			text-shadow: none;
			text-align: center;

			div {
				&:hover {
					background: $main_menu_background_color;
					border-radius: 0;
					color: $white;
					font-weight: 400;
					text-shadow: none;
				}
			}

			div.areaSelected {
				background: $main_menu_background_color;
				border-color: $main_menu_background_color;
				border-radius: 0;
				color: $white;
				font-weight: 400;
			}
		}
	}

	#ihf-status-fields {
		input {
			outline: 0;

			&:focus {
				outline: 0;
			}
		}
	}

	.form-control {
		outline: 0;
		box-shadow: none;
		border-radius: 0 !important;

		&:focus {
			outline: 0;
			border-color: #cccccc;
			box-shadow: none;
		}
	}

	input.form-control {
		outline: 0;
		box-shadow: none;
		border-radius: 0 !important;

		&:focus {
			border-color: #cccccc;
			box-shadow: none;
		}
	}

	.checkbox {
		input[type=checkbox] {
			outline: 0;

			&:focus {
				outline: 0;
			}
		}
	}

	#ihf-main-search-form-submit,
	#ihf-search-adv-submit,
	#ihf-search-adv2-submit {
		background: $main_menu_background_color;
		color: $white;
		border-radius: 0;
		border: none;
		box-shadow: none;
		font-size: 14px;
		outline: 0;
		text-shadow: none;
		margin-top: 10px;
		padding: 10px 15px;

		&:hover {
			border: none;
			background: $main_menu_background_color_2;
		}

		&:focus {
			border: none;
			outline: 0;
		}
	}

	.ihf-advanced-search-launch {
		color: $label_color;
		text-decoration: none;
		font-size: 14px;

		&:hover {
			color: $linkColorHover;
			text-decoration: none;
		}
	}

	.chosen-container-multi {
		.chosen-choices {
			border-radius: 0;
		    box-shadow: none;
		    text-shadow: none;
		    background-image: none;

		    li.search-choice {
		    	background-image: none;
			    box-shadow: none;
			    border-radius: 0;
			    color: $label_color;
			    text-shadow: none;
		    }
		}

		.chosen-results {
			li.highlighted {
				background-image: none;
				background-color: $main_menu_background_color;
				color: $white;
			}
		}
	}

}


#emailSignupForm {
	padding: 0 15px 15px;

	label {
		font-size: 14px;
	}

	input.form-control {
		border-radius: 0 !important;
		box-shadow: none;

		&:focus {
			outline: 0;
			box-shadow: none;
			border-color: #ccc;
		}
	}

	.ihf-email-alert-widget-submit {
		border-radius: 0;
		box-shadow: none;
		border: none;
		background: $main_menu_background_color;
		text-shadow: none;

		&:hover {
			background: $main_menu_background_color_2;
		}
	}
}


.ihf-map-icon {
	background: $main_menu_background_color !important;
	border-color: $main_menu_background_color !important;
	color: $white;
	border-radius: 0;
	box-shadow: none;
	margin-top: 10px;

	&:after {
		border-top-color: $main_menu_background_color !important;
		left: 7px;
	}
}


.leaflet-touch {
	.leaflet-bar,
	.leaflet-control-layers {
		border: 1px solid #ccc;
    	border-radius: 0;

    	a {
    		border-radius: 0;
    	}
	}
}


#ihf-main-container {
	.leaflet-popup-content-wrapper {
		box-shadow: none;
		border-radius: 0;
	}

	.leaflet-popup-content-wrapper,
	.leaflet-popup-tip {
		box-shadow: none;
	}
}

#ihf-main-container {
	.close {
	    color: $white;
	    opacity: 0.7;
	    text-shadow: none;

	    &:hover {
	    	color: $white;
	    	opacity: 1;
	    }

	    &:focus {
	    	outline: 0;
	    }
	}

	.form-control,
	input.form-control,
	select.form-control {
		border-radius: 0 !important;
		box-shadow: none;
		color: $label_color;

		&:focus {
			box-shadow: none;
			outline: 0;
			border-color: #ccc;
		}
	}

	select.form-control {
		-webkit-appearance: none;
		background-image: url( '../../images/dropdown-arrow.png' );
		background-position: right 50%;
		background-repeat: no-repeat;
	}

	.input-group-addon {
		border-radius: 0;
	}

	.panel {
		border-radius: 0;

		.panel-heading {
			border-top-right-radius: 0;
			border-top-left-radius: 0;
		}

		.panel-body {
			font-size: 14px;
		}
	}

	.chosen-container {
		margin-bottom: 10px;
	}

	label.field-label {
		font-size: 14px;
	}

	.ihf-map-results-count {
		margin: 0;
	}

	button.btn-default,
	button.btn-primary {
		background: $main_menu_background_color;
		color: $white;
		border-radius: 0;
		box-shadow: none;
		border-color: $main_menu_background_color;
		font-size: 14px;
		text-shadow: none;
		padding: 10px 15px;

		&:hover {
			background: $main_menu_background_color_2;
			border-color: $main_menu_background_color_2;
			color: $white;
		}

		&:focus {
			outline: 0;
			background: $main_menu_background_color_2;
			border-color: $main_menu_background_color_2;
			color: $white;
		}
	}

	.btn-link {
		color: $label_color;
		text-decoration: none;

		&:hover {
			color: $linkColorHover;
			text-decoration: none;
		}
	}

	.searchAddressForm,
	.form-inline {
		button.btn-primary,
		button.btn-default {
			padding: 5px 12px;
		}
	}

	#Map {
		overflow: hidden;
	}

	.checkbox {
		input {
			outline: 0;

			&:focus {
				outline: 0;
			}
		}
	}

	.well-sm {
		padding: 15px;
		border-radius: 0;
		box-shadow: none;
	}
}


.optima-express {
	#ihf-main-container {
		.ihf-widget {
			padding: 0;

			.form-inline,
			.searchAddressForm {
				.row {
					padding: 0 0 15px 0;

					& > div {
						padding-left: 0;
					}
				}
			}
		}
	}
}

.ihf-container {
	.ihf-one-selectedArea {
		border-radius: 0;
		padding: 0;

		.btn {
			transition: 0.1s all linear;

			&:hover {
				background: #dadada;
				transition: 0.1s all linear;
			}
		}
	}
}
