
/* Agents Listing
--------------------------------------------------------*/
.about-agent{
  padding: 16px 20px 0;
  border: 1px solid $border_color_two;
  margin-bottom: 30px;

  h4 a{
    color: $headingsColor;
    &:hover{
      color: $linkColorHover;
    }
  }

  .agent-pic{
    margin: 0;
    float: none;
    margin-bottom: 15px;
  }

  //.detail{
  //  float: right;
  //  width: 71.5%;
  //  margin-bottom: 10px;
  //}

  .contact-types{
    color: $headingsColor ;
    font-family: $RobotoFontFamily;
  }

  .contacts-list{
    margin: 0 0 20px;
    li {
      border: none;
      display: inline-block;
      margin-right: 20px;
      line-height: 20px;
      &:last-child{
        margin-right: 0;
      }

      svg {
        width: 16px;
        height: 16px;
      }
    }
  }

  .agent-recaptcha{
    min-height: inherit;
  }

  .follow-agent{
    border-top: 1px solid $border_color_two;
    margin: 40px -20px 0;
    clear: both;
  }

  .real-btn.btn {
    float: right;
    padding: 12px 25px;
  }

  .social_networks {
    li {
      a {
        border-color:  $border_color_two;
        height: 44px;
        i{
          width: auto;
          &.icon-twitter:before {
            left: 1px;
            position: relative;
            top: 1px;
          }
          &.icon-google-plus:before {
            left: 2px;
            position: relative;
            top: 1px;
          }
          &.icon-linkedin:before {
            left: 1px;
            position: relative;
            top: 0px;
          }
        }
      }
    }
  }

}


/* Agent Single
--------------------------------------------------------*/
.about-agent.agent-single{

  figure{
    width: auto;
    margin: 0;
    float: none;
    img {
      border: 1px solid $border_color_two;
    }
  }

  .detail{
    float: none;
    width: auto;
    margin-bottom: 0;
    ul, ol {
      list-style-position: inside;
    }
  }

}


.agent-single{

  .contacts-list{
    margin: 0 0 20px;
    li {
      border: none;
      display: inline-block;
      margin-right: 20px;
      &:last-child{
        margin-right: 0;
      }
    }
  }

  .follow-agent{
    margin-top: 0;
  }

}


/* Agent Single Form
--------------------------------------------------------*/
#agent-single-form{
  margin-top: 15px;
  margin-bottom: 0;

  input[type="text"],
  input[type="email"],
  textarea{
    width: 100%;
    box-sizing: border-box;
    &.error{
      border: 1px solid #ff1a3a;
      box-shadow: inset 0 0 1px #ff1a3a;
    }
  }

  input[type="text"],
  input[type="email"]{
    height: 34px;
  }

  textarea{
    height: 100px;
  }

  .inspiry-recaptcha-wrapper {
    margin-bottom: 10px;
  }

  .recaptcha_widget{
    width: 100%;
    box-sizing: border-box;
    max-width: none;
  }

  #ajax-loader{
    display: none;
  }

  #error-container,
  #message-container{
    margin: 10px 0;
    padding: 0;
  }

  ul.recaptcha_options{
    margin: 5px;
  }

}