@media (min-width: 980px) {
  body.home {
    .dsidx-results {
      *zoom: 1;
      &:before, &:after { display: table; content: ""; line-height: 0; }
      &:after { clear: both; }

      .dsidx-prop-summary {
        width: 46.3%;
        float: left;

        &:nth-child(2n){
          margin-left: 18px;
          margin-bottom: 18px !important;
        }

        &:nth-child(2n+1){
          clear:left;
        }

        .dsidx-prop-title {
          & + div {
            margin-top: -85px;
          }
        }


        @media (min-width: 980px) and (max-width: 1200px) {

          width: 45.3%;

          .dsidx-prop-title {
            margin-left: 0 !important;
            margin-bottom: 10px !important;

            & + div {
              margin-top: -10px;
              width: 215px;
            }
          }

          .dsidx-prop-features div {
            padding-left: 15px;
            width: 40%;
          }
        }

      }
    }
  }
}