/**
 * MailChimp Integration
 *
 */

.mc4wp-form-fields {
	p {
		margin-bottom: 5px;
	}

	input {
		box-sizing: border-box;
		height: auto;
		padding: 6px;
		width: 100%;
	}

	input[type="submit"] {
		background: $main_menu_background_color;
		border: none;
		color: $white;
		outline: 0;
		padding: 10px;
		-webkit-transition: 0.1s background linear;
		transition: 0.1s background linear;

		&:focus {
			outline: 0;
		}

		&:hover {
			background: $main_menu_background_color_2;
			-webkit-transition: 0.1s background linear;
			transition: 0.1s background linear;
		}
	}
}

.sidebar {
	.mc4wp-form {
		padding: 0 15px 15px;
	}
}
