
/* Compare Listings
--------------------------------------------------------*/

.compare-template {
    padding: 1rem 0 0 2rem;

	.compare-feature-column {
		opacity: 0;

		.property-thumbnail {
			margin-left: -52px;
			background: $white;
			z-index: 9999;
		}

		p {
			margin-left: -52px;
			margin-bottom: 0;
		    padding: 10px 10px 10px 30px;
		    background: $grayLighter;
		    font-weight: 700;
		    color: $label_color;

		    &:nth-child(2n+1) {
		    	background: $white;
		    }
		}
	}
	.compare-properties-column {
		opacity: 0;
		width: 160px;
		position: relative;

		p {
			margin-left: -20px;
			margin-bottom: 0;
		    padding: 10px 10px 10px 20px;
		    background: $grayLighter;

		    .fa-close {
		    	color: $red;
		    }

		    .fa-check {
		    	color: $green;
		    }

		    &:nth-child(odd) {
		    	background: $white;
		    }
		}

		&:nth-last-child(1) {
			.property-thumbnail {
				margin-right: -26px;
				padding-right: 26px;
			}
	    	p {
	    		margin-right: -26px;
	    	}
	    }

	    .feature-label {
			display: none;
	    }

	    .stuck {
			position:fixed;
			top:0;
			width: 160px;
		}

		.property-thumbnail {
			display: block;
			width: 100%;
			background: #ffffff;
			margin-left: -20px;
			padding-left: 20px;
			z-index: 9999;

			a {
				display: block;
				width: 100%;
				color: $label_color;

				&:hover {
					color: $linkColorHover;
				}
			}

			img {
				margin-bottom: 10px;
				max-height: 200px;
			}

			h5 {
				margin: 0 auto;
				padding: 10px 0;
			}

			p {
				display: block;
				margin-left: 0;
				margin-bottom: 0;
				padding: 0 0 5px;
				background: $white;
				font-weight: 700;
			}

			.property-price {
				color: #4dc7ec;
			}
		}
	}

}

.inner-wrapper {
	.remove-overflow {
		overflow: visible;
	}
}
