
/* Header
--------------------------------------------------------*/
.header-wrapper{
  background-color: $header_Wrapper_background_color;
  &.stick {
    position: fixed;
    z-index: 9999;
    width: 100%;
    top: 0;
  }
}
.disable-responsive .header-wrapper{
  min-width: 940px;
}

/*  Header
--------------------------------------------------------*/
#header{
  position: relative;
}

/*  Header Top
--------------------------------------------------------*/
#header-top{
  border-bottom: 1px solid $border_color_one;
  margin-bottom: 30px;

  #lang_sel{
    float: left;
    height: auto;
    margin-top: 10px;
    margin-right: 10px;
    a{
      background-color: #252A2B;
      border-color: #343A3B;
      color: #929A9B;
      &:hover{
        color: darken(#929A9B,10%);
      }
    }
    ul ul{
      border-top-color: #343A3B;
    }
    img.iclflag{
      top: 0;
    }
  }

  #lang_sel_click{
    float: left;
    height: auto;
    margin-top: 10px;
    margin-right: 10px;
    a{
      background-color: #252A2B;
      border-color: #343A3B;
      color: #929A9B;
      &:hover{
        color: darken(#929A9B,10%);
      }
    }
    ul ul{
      border-top-color: #343A3B;
    }
    img.iclflag{
      top: 0;
    }
  }

  #lang_sel_list{
    float: left;
    &.lang_sel_list_vertical{
      width: auto;
      margin-top: 11px;
      ul{
        border: none;
        border-top-color: #343A3B;
        li{
          width: auto;
        }
      }
      a:visited,
      a{
        border: none;
        background-color: #252A2B;
        border-color: #343A3B;
      }
    }
    &.lang_sel_list_horizontal{
      height: auto;
      margin-top: 14px;
    }
    ul li a{
      background: transparent;
    }
    ul li a.lang_sel_sel{
      color: #b5b5b5;
    }
    ul li a.lang_sel_other{
      color: #858585;
    }
    img.iclflag{
      top: -1px;
    }
  }

}

/* Header currency switcher */
#currency-switcher-form{
  display: inline-block;
  float: left;
  margin: 10px 10px 0 0;
}

#currency-switcher{
  margin: 0;
  position: relative;
  #selected-currency{
    border: 1px solid #343A3B;
    width: 30px;
    height: 24px;
    font-size: 11px;
    line-height: 24px;
    padding: 0 10px;
    background-color: #252A2B;
    color: #929A9B;
    cursor: pointer;
    position: relative;
    &:after{
      position: absolute;
      top: 0;
      right: 5px;
      content: "\f0d7";
      font-family: FontAwesome;
    }
    &:hover{
      background-color: #2d3334;
      color: #b0babb;
    }
  }
}
#currency-switcher-list{
  margin: 0;
  list-style: none;
  position: absolute;
  top: 24px;
  left: 0;
  z-index: 9999;
  display: none;
  li{
    background-color: #252A2B;
    color: #929A9B;
    border: 1px solid #343A3B;
    border-top: none;
    width: 30px;
    height: 24px;
    font-size: 11px;
    line-height: 26px;
    padding: 0 10px;
    cursor: pointer;
    &:first-child{
      border-top-width: 1px;
      border-top-style: solid;
    }
    &:hover{
      background-color: #2d3334;
      color: #b0babb;
    }
  }
}


/*  Contact Email
--------------------------------------------------------*/
#contact-email{
  font-size: $baseFontSize;
  line-height: $baseLineHeight;
  color: $header_text_color;
  float: left;
  font-weight: normal;
  padding: 13px 0;
  margin: 0 0 0 6px;
  .email{
    background: url(../images/icon-mail.png) no-repeat left 0 ;
    display: inline-block;
    height: 16px;
    width: 16px;
    margin-right: 8px;
    vertical-align: top;
  }
  svg{
    display: inline-block;
    margin-right: 5px;
    vertical-align: top;
    .path{
      fill: #929a9b;
    }
  }
  a{
    color: $header_text_color;
    &:hover{
      color: $header_link_hover_color;
    }
  }
}


/*  Social Networks
--------------------------------------------------------*/
.single article .social_networks{
  margin: 0;
}
.social_networks{
  float: right;
  list-style: none;
  margin: 0;
  padding:0;
  li{
    display:  inline-block;
    border:none;
    margin:0 0 0 -4px;
    a{
      color: $header_text_color;
      font-size: 15px;
      display:block;
      text-align: center;
      line-height: 44px;
      width:44px;
      height:46px;
      margin:0px;
      padding:0;
      outline: none;
      border-left: 1px solid $border_color_one;
      @include transition(0.3s);
      &:hover{
        @include transition(0.3s);
        color: $white;
      }
    }
    i.fa-lg{
      font-size: 1em;
    }
  }
}
.facebook a{
  &:hover{
    background-color: #395b89;
  }
}
.twitter a{
  &:hover{
    background-color: #2bbfec;
  }
}
.rss a{
  &:hover{
    background-color:#ec894d;
  }
}
.gplus a{
  &:hover{
    background-color: #d63b3b;
  }
}
.linkedin a{
  &:hover{
    background-color: #1184cd;
  }
}
.youtube a{
  &:hover{
    background-color: #d02525;
  }
}
.skype a{
  &:hover{
    background-color: #0099e5;
  }
}
.instagram a{
  &:hover{
    background-color: #457299;
  }
}
.pinterest a{
  &:hover{
    background-color: #be2225;
  }
}

/* User Nav
--------------------------------------------------------*/
.user-nav{
  float: right;
  padding: 0;
  margin: 14px 10px 10px;
  a{
    display: inline-block;
    color: $header_text_color;
    font-size: 12px;
    line-height: 18px;
    margin: 0;
    font-weight: bold;
    &:after {
      content: "\f068";
      margin: 0 8px;
      font-family: FontAwesome;
      font-size: 7px;
      color: #929A9B;
      opacity: 0.8;
      position: relative;
      top: -2px;
    }
    &:last-child:after{
      content: '';
      margin: 0 8px 0 0;
    }
    &:hover{
      color: $header_link_hover_color;
    }
    i{
      margin-right: 7px;
      font-size: 14px;
    }
  }
}


/*  Logo
--------------------------------------------------------*/
#logo{
  float: left;
  max-width: 100%;
  margin: 0 auto ;
  a,img{
    display: inline-block;
    max-width: 100%;
  }
  img{
    margin-bottom: 20px;
  }
  h2{
    margin: 25px 0 15px;
    font-size: 30px;
    line-height: 36px;
    a{
      color: #fff;
      &:hover{
        color:$contact_number_bg_color;
      }
    }
  }
  h2.logo-heading{
    position: relative;
  }
}


/*  Tag Line
--------------------------------------------------------*/
.tag-line{
  margin-top: -10px ;
  font-size: 11px;
  margin-left: 0px;
  text-align: left;
  span{
    display: inline-block;
    background-color: $border_color_one;
    padding: 0 10px 1px;
  }
}


/*  Menu and Contact Wrap
--------------------------------------------------------*/
.menu-and-contact-wrap{
  position: absolute;
  bottom: 0;
  right: 0;
}



/*  Main Nav
--------------------------------------------------------*/
$main_menu_color:  #afb4b5;
$main_menu_hover_color: $white;
$main_menu_a_padding: 14px 10px ;
$main_menu_a_padding_2: 10px 10px 12px;
$main_menu_ul_width: 220px;
$main_menu_ul_top: 48px;

.main-menu{
  margin-top: 42px;
  float: right;
  ul {
    list-style: none;
    margin: 0;
    li {
      float: left;
      position: relative;
      &.current-menu-ancestor,
      &.current-menu-parent,
      &.current-menu-item,
      &.current_page_item,
      &:hover{
        > a{
          color: $main_menu_hover_color ;
          background-color: $main_menu_background_color ;
        }
      }
      a {
        font-family: $headingsFontFamily;
        color: $main_menu_color;
        font-size: 14px;
        display: block;
        text-decoration: none;
        padding: $main_menu_a_padding;
      }
      ul{
        display: none;
        background-color: $main_menu_background_color ;
        width: $main_menu_ul_width;
        padding: 0;
        position: absolute;
        top: $main_menu_ul_top;
        left: 0;
        z-index: 888;
        li{
          float: none;
          margin: 0;
          &:hover{
            > a{
              background-color: $main_menu_background_color_2 ;
            }
          }
          a{
            font-size: 13px;
            padding: $main_menu_a_padding_2;
            color: $main_menu_hover_color ;
          }
          ul{
            background-color: $main_menu_background_color_2 ;
            top: 0px;
            left: $main_menu_ul_width;
            li{
              &:hover{
                > a{
                  background-color: $main_menu_background_color_3 ;
                }
              }
            }
          }
        }
      }
      .menu-item-desc {
        display: inline-block;
        float: right;
        color: $main_menu_background_color;
        background: $main_menu_hover_color;
        padding: 0 3px;
        font-size: 11px;
        line-height: 16px;
        border-radius: 2px;
        margin-top: 2px;
      }
    }
  }

  // Responsive Nav
  .rh_menu__responsive {
    display: none;
    margin: 0 auto;
    width: 100%;
    padding: 10px 0;

    li {
      float: none;

      ul {
        display: none;
        width: 100%;
      }
    }

    .rh_menu__indicator {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 48px;
      position: absolute;
      right: 0;
      top: 0;
      line-height: 0.4;
      cursor: pointer;
      color: #fff;
      background: none;
      outline: 0;
      -webkit-tap-highlight-color: rgba(#fff, 0);
      transition: 0.2s all linear;

      &:focus {
        background: none;
        outline: 0;
      }
    }

    .rh_menu__indicator_up {
      transform: rotate(180deg);
      transition: 0.2s all linear;
    }
  }

  .rh_menu__hamburger {
    display: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);

    p {
      display: inline-block;
      vertical-align: top;
      font-size: 16px;
      margin: 0 0 0 5px;
      line-height: 1.3;
      color: #fff;
    }
  }
}


/*  Contact Number
--------------------------------------------------------*/
.contact-number{
  float: right;
  position: relative;
  margin: 42px 0 0;
  font-size: 20px;
  color: $contact_number_color ;
  min-height: 48px;
  line-height: 42px;
  background-color: $contact_number_bg_color;

  span,
  a{
    color: inherit;
  }

  .fa-phone{
    background-color: #37b3d9 ;
    margin-right: 10px;
    padding: 12px 15px 12px 15px;
    font-size: 24px;
    position: relative;
  }
  .outer-strip{
    display: block;
    width: 88%;
    height:100%;
    position: absolute;
    right: -88%;
    bottom: 0;
    background-color: $contact_number_bg_color ;
  }
}

/*  Double Phone Number fix for IE8 */
.lt-ie9 .mobile-version { display: none; }

@media ( min-width: 768px){
  .mobile-version{
    display: none;
  }
}

@media ( max-width: 767px){
  .mobile-version{
    display: inline-block;
  }
  .desktop-version{
    display: none;
  }
}


/*  Header Variation: Center;
--------------------------------------------------------*/
.header__top {
  padding: 20px 0 0 0;

  .header__switchers {
    display: inline-block;
    text-align: left;
  }

  #logo {
    float: none;
    text-align: center;
    margin-left: auto;
    margin-right: 0;
    float: none;

    img {
      margin: 10px 0;
    }
    .logo-heading {
      margin: 10px 0;
    }

    a {
      max-width: 300px;
    }
  }
}

.switcher__lang {
  display: inline-block;
  vertical-align: top;
  width: 155px;
  padding: 7px 10px 0 0;
  text-align: left;

  #lang_sel {
    li {
      width: 155px;

      a {
        font-size: 13px;
        color: rgba( $white, 0.5 );
        background: $header_Wrapper_background_color;
        border: none;
        border-bottom: 1px solid rgba( $white, 0.5 );
        padding: 5px 0;

        &:hover,
        &:visited {
          color: rgba( $white, 0.5 );
          background: $header_Wrapper_background_color;
        }
      }

      ul {
        border-top: none;
        top: 35px;

        a {
          padding: 10px 5px;
          background: $header_Wrapper_background_color !important;
          border: 1px solid #343A3B;
          &:hover {
            background: lighten($header_Wrapper_background_color, 5%) !important;
          }
        }
      }
    }
  }
}

.switcher__currency {
  display: inline-block;
  vertical-align: top;
  width: 100px;
  padding: 7px 10px 0 0;
  text-align: left;

  #currency-switcher-form {
    margin: 0;
    float: none;
    width: 100px;

    #selected-currency {
      display: block;
      width: 100%;
      font-size: 14px;
      height: 14px;
      line-height: 14px;
      padding: 10px 0;
      color: rgba( $white, 0.5 );
      background: $header_Wrapper_background_color;
      border: none;
      border-bottom: 1px solid rgba( $white, 0.5 );

      &:after {
        padding: 10px 0 0 0;
        right: 1px;
      }
    }

    #currency-switcher-list {
      top: 35px;
      width: 100px;

      li {
        display: block;
        width: 100%;
        font-size: 14px;
        color: rgba( $white, 0.5 );
        background: $header_Wrapper_background_color;
        box-sizing: border-box;
        padding: 3px 10px;
        height: 34px;

        &:hover {
          background: lighten($header_Wrapper_background_color, 5%);
        }
      }
    }
  }
}

#social_nav {
  display: inline-block;
  margin-top: 7px;

  .social_networks {
    float: none;

    li {
      margin: 0;

      a {
        width: 36px;
        border-left: none;
        height: 16px;
        padding: 10px 0;

        i {
          line-height: initial;
          vertical-align: top;
        }
      }
    }
  }
}

.header__logo {
  a {
    display: inline-block;
    text-align: center;
    color: $white;

    &:hover {
      color: $contact_number_bg_color;
    }
  }

  .tag-line {
    margin-top: 3px;
    margin-left: 0;
    text-align: center;
  }
}

.header__user_nav {
  display: inline-block;
  text-align: right;
  margin-top: 0;
  margin-left: 10px;

  #contact-email {
    display: inline-block;
    float: none;
    padding: 16px 8px 0 0;
    margin: 0;
  }

  .user-nav {
    padding: 16px 0 0;
    margin: 0;

    a {
      &:after {
        display: inline-block;
        content: "\f111";
        font-size: 5px;
        margin: 0 8px;
      }
      &:last-child {
        &:after {
          display: none;
        }
      }
    }
  }
}

.header__navigation {
  .header__menu {
    display: inline-block;
    width: initial;
    float: left;

    .main-menu {
      float: none;
      margin: 30px 0 0 0;
    }
  }

  .header__phone_number {
    display: inline-block;
    width: initial;
    float: right;

    .contact-number {
      margin: 30px 0 0;
      line-height: 1.5;

      i {
        line-height: 24px;
      }
    }
  }
}
