
/* Detail Page
--------------------------------------------------------*/
#property-featured-image{
  img{
    width: 100%;
    height: auto;
  }
}
#property-detail-flexslider {
  background-color: #292d2d;
  overflow:hidden;
  height: 304px;
  position: relative;
  .flexslider {
    border: none;
    margin: auto;
    border-radius: 0;
  }
  .flex-direction-nav{
    position: absolute;
    top: 0;
    left: 0;
  }
  .flex-viewport{
    float: left;
    overflow:hidden;
  }
  .flex-viewport,
  .flex-direction-nav{
    width: 607px;
    height: 304px;
  }
  .flexslider .slides img{
    width: auto;
  }
  .flex-direction-nav{
    pointer-events: none;
    a::before{
      color: #fff;
      text-shadow: none;
    }
    .flex-next,
    .flex-prev{
      pointer-events: auto;
      font-size: 0;
      text-indent: 0;
      color: #fff;
      z-index: 999;
      margin-top: -25px;
      text-align: center;
      background-color: #292d2d;
      width: 35px;
      height: 50px;
      line-height: 50px;
    }
    .flex-next{
      right: 0px;
    }
    .flex-prev{
      left: 0px;
    }
    .flex-next:before,
    .flex-prev:before{
      font-family: FontAwesome;
      font-size: 20px;
    }
    .flex-next:before{
      content: "\f105";
    }
    .flex-prev:before{
      content: "\f104";
    }
  }
  .flex-control-thumbs{
    li{
      float: none;
      margin-bottom: 5px;
      width: 100% ;
      height: 48px;
      img{
        height: 100%;
      }
    }
  }
  .jcarousel-container-vertical{
    float: right;
    width: 77px;
    padding: 7px;
    background-color: #292d2d;
    margin: 0;
  }
  .jcarousel-clip-vertical{
    overflow: hidden;
    height: 260px;
  }
  .jcarousel-next,
  .jcarousel-prev{
    margin: 6px 0 2px;
    text-indent: -999em;
    display: block;
    width: 38px;
    height: 28px;
    cursor: pointer;
    &:hover {
      background-position: center -28px;
    }
  }
  .jcarousel-next{
    background: url(../../images/slideshow-nav-right.png) #141616 no-repeat center 6px;
    float: left;
  }
  .jcarousel-prev{
    background: url(../../images/slideshow-nav-left.png) #141616 no-repeat center 6px;
    float: right;
  }
}


#overview {

  font-family: $RobotoFontFamily;
  background-color: $white;

  .property-item{
    padding: 0 ;
    border: none;
    margin-bottom: 0;
    background: transparent;

    .wrap{
      background-color: $border_color_two;
      margin-bottom: 16px;
    }

    .title{
      float: left;
      padding: 0 0 0 16px;
      font-size: 14px;
      line-height: 40px;
      color: $headingsColor;
      background: none;
      margin: 0;
      border: none;
    }

    .price {
      background: url(../../images/arrow-black.png) no-repeat left 0 ;
      float: right;
      margin: 0;
      padding: 0 0 0 15px;
      font-family: $RobotoFontFamily;
      span{
        display: inline-block;
        background-color: #4DC7EC;
        padding: 11px 10px 11px 14px;
        margin-right: -1px;
        color: $white;
      }
      .status-label{
        margin-right: -5px;
        padding-right: 28px;
        padding-left: 7px;
        background: url(../../images/arrow-left.png) #292d2d no-repeat right 0 ;
      }
      small{
        vertical-align: 1px;
        color: #fff;
      }
    }

    .property-meta {
      border: 1px solid $border_color_two;
      margin: 0 17px 18px;
      padding: 0 ;
      background-color: #f5f5f5;
      border-color: #dedede;
      width: auto;
      span{
        margin: 0 0 0 10px;
        padding-right: 10px;
        padding-top: 11px;
        border-color: #f5f5f5;
        color: $headingsColor;
        min-height: 22px;
        min-width: 86px;
        height:auto !important;
        height: 22px;

        img {
          margin-right: 8px;
          margin-top: 2px;
          vertical-align: top;
        }
      }
    }

    .printer-icon{
      display:  inline-block;
      float: right;
      padding-left: 15px;
      padding-bottom: 11px;
      border-left: 1px solid $border_color_two;
      margin-left: 0;
      a{
        color: $headingsColor;
        .fa{
          color: #586162;
          margin-right: 6px;
          font-size: 15px;
        }
        &:hover{
          color: $main_menu_background_color;
          .fa{
            color: $main_menu_background_color;
          }
        }
      }
    }

    .content{
      padding: 0 20px 26px;
      margin-bottom: 0;
      font-size: 12px;
      line-height: 22px;
      border-bottom: 1px solid $border_color_two;
    }

    h4.additional-title{
      font-weight: 500;
    }

    ul.additional-details{
      list-style: none;
      margin: 0;
      border-top: 1px solid #f1f1f1;
      li{
        padding: 6px 5px 6px 10px;
        border-bottom: 1px solid #eee;
        margin: 0;
        &:hover{
          background-color: #f1f1f1;
        }
        strong{
          display: inline-block;
          font-weight: 500;
          color: $headingsColor;
          margin-right: 10px;
        }
      }
    }
    .features{
      background-color: #fbfbfb;
      border-bottom: 1px solid $border_color_two;
      padding-bottom: 0;
      h4{
        border-bottom: 1px solid $border_color_two;
        background-color: #f8f8f8;
        float: none;
        padding: 9px 20px 10px;
        line-height: 20px;
      }

      .arrow-bullet-list {
        margin: 10px 10px 10px;
        li {
          position: relative;
          float: left;
          width: 25%;
          margin:0 27px 2px 0;
          padding-left: 25px;
          list-style: none;

          &:before {
            content: '';
            background: url(../../images/feature-list-bullet.png) no-repeat 10px 14px;
            position: absolute;
            left: 0;
            top: 0;
            width: 20px;
            height: 34px;
          }

          a {
            display: block;
            color: #636464;
            padding: 7px 2px 7px 0px;
          }

          &.selected,
          &:hover {
            background-color: #f1f1f1;
          }
        }
      }
    }
  }

  .common-note{
    margin-top: 20px;
    background: #fafafa;
    padding: 20px 15px 20px 20px;
    border: 1px solid #eee;
    .common-note-heading {
      margin-bottom: 10px;
      font-weight: 500;
    }
    p {
      color: darken( $textColor , 10% );
      margin-bottom: 0;
    }
  }

  .property-video{
    border-bottom: 1px solid $border_color_two;
    padding: 24px 25px;
    position: relative;
    img{
      margin-bottom: 0;
    }
  }

  .property-virtual-tour {
    border-bottom: 1px solid $border_color_two;
    padding: 24px 25px;
    position: relative;

    iframe {
      height: 420px;
    }
  }

  .property-video .play-btn {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -37px;
    margin-left: -37px;
    height: 74px;
    width: 74px;
    background: url(../../images/play-btn2.png)  no-repeat center center;
  }
  .share-label,
  .common-label{
    background-color: #586162;
    display: inline-block;
    padding: 6px 17px 8px 14px;
    color: $white;
    font-family: $headingsFontFamily;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 0.5px;
  }

  .video-label{
    @extend  .common-label;
  }

  .virtual-tour-label {
    @extend  .common-label;
  }

  .map-wrap{
    padding: 24px 25px 10px;
    position: relative;
    border-bottom: 1px solid $border_color_two;
  }

  .attachments-label,
  .map-label{
    @extend  .common-label;
    padding-left: 20px;
    padding-right: 25px;
    z-index: 1000;
  }

  .attachments-wrap{
    padding: 25px 25px 25px 25px;
    position: relative;
    border-bottom: 1px solid $border_color_two;
  }

  .attachments-inner{
    border: 1px solid $border_color_two;
    background-color: #f5f5f5;
    padding: 15px 10px 15px 15px;
  }

  .attachments-list{
    list-style: none;
    padding: 0;
    margin: 0;
    li{
      float: left;
      width: 31%;
      padding: 10px 0 10px 10px;
      i{
        font-size: 24px;
        margin-right: 10px;
        vertical-align: middle;
      }
    }
  }

  .share-networks{
    margin: 0 0 14px;
    border: 1px solid $border_color_two;
    background-color: #f5f5f5;

    a{
      font-size: 13px;
      color: $headingsColor;
      &:hover{
        color: $main_menu_background_color_2;
      }
    }

    a [class^="fa-"], a [class*=" fa-"] {
      margin: 0 9px 0 12px;
      color: #929a9b;
    }

    span{
      display: block;
      float: left;
      padding: 11px 23px 11px 0;
      border-right: 1px solid $border_color_two;
      &:last-of-type {
        border-right: none;
      }
    }
    .share-label{
      position: static;
      margin: -1px 6px -1px -1px;
      padding: 12px 21px 13px 17px;
      border-right: none;
    }

  }

  .property-views {

    .views-label {
      @extend .common-label;
      margin-bottom: 15px;
    }

    border-bottom: 1px solid #dedede;
    padding: 24px 25px 15px;
  }

  .child-properties{
    border-bottom: 1px solid $border_color_two;
    background-color: $white;
    padding: 10px 24px 0;
    h3{
      font-family: $RobotoFontFamily;
      font-size: 20px;
      font-weight: normal;
      margin: 0 0 5px;
      text-transform: capitalize;
    }
    .property-item{
      margin-bottom: 30px;
      figure{
        margin: 0 20px 20px 0;
      }
      h4{
        font-size: 16px;
        margin-bottom: 10px;
      }
      .price{
        color: #4dc7ec;
        font-weight: bold;
        font-size: 14px;
        background: none;
        float: none;
        padding: 0;
        margin: 0 0 10px;
        small{
          color: #4dc7ec;
          display: inline-block;
          font-weight: bold;
          margin-left: 5px;
        }
      }
      p{
        margin-bottom: 10px;
      }
      .property-meta{
        margin: 0;
      }
    }
  }

  .agent-detail{
    border-bottom: 1px solid $border_color_two;
    background-color: $white;
    padding: 16px 24px;
    h3{
      font-family: $RobotoFontFamily;
      font-size: 20px;
      line-height: 24px;
      font-weight: normal;
      margin: 0 0 10px;
      text-transform: capitalize;
    }
    p{
      font-size: 13px;
      line-height: 22px;
      margin: 0 0 20px;
    }
  }

  .left-box{
    float: left;
    width: 100%;
    figure{
      float: left;
      margin: 4px 25px 20px 0;
      width: 212px;
      height: auto;
      img{
        border: 1px solid $border_color_two;
        width: 210px;
        height: auto;
      }
    }
    p a.real-btn{
      margin-top: 10px;
    }
  }

  .contact-form{
    float: right;
    width: 100%;
    h3{
      margin-bottom: 6px;
    }
    form {
      margin: 0 0 10px;
    }
    textarea,
    input[type="text"]{
      color: #7b8283;
      border: 1px solid $border_color_two;
      margin-bottom: 16px;
      background-color: $white;
      width: 96.6%;
      padding: 3px 10px 5px;
    }
    textarea {
      height: 96px;
      resize: none;
      margin-bottom: 15px;
    }
    .real-btn {
      // padding: 7px 26px;
    }
    .inspiry-recaptcha-wrapper{
      margin-bottom: 15px;
    }
    #recaptcha_widget{
      margin-bottom: 15px;
      .recaptchatable *{
        font-size: 6pt;
      }
      #recaptcha_response_field{
        font-size: 10pt;
      }
    }
    .ajax-loader{
      display: none;
    }
    .form-separator{
      margin: 0 0 15px;
    }
    .error-container{
      color: red;
    }
  }

  .contacts-list{
    float: left ;
    width: 61.5%;
    margin-bottom: 8px;
  }

}

#property_map {
  border: none;
  width: 100%;
  height: 350px;
  margin-bottom: 15px;
  img{
    max-width: none;
  }
}

.infoBox{
  > img{
    z-index: 999;
    background: rgb( 240, 240, 240 );
    background: rgba( 255, 255, 255, 0.6 );
    &:hover{
      background: rgb( 255, 255, 255 );
      background: rgba( 255, 255, 255, 0.9 );
    }
  }
  .map-info-window{
    position: relative;
    background: #fff;
    width: 244px;
    border-bottom: 3px #4dc7ec solid;
    margin-bottom: 17px;
  }
  .arrow-down{
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 10px 0 10px;
    border-color: #4dc7ec transparent transparent transparent;
    position: absolute;
    bottom: -13px;
    left: 112px;
  }
  .prop-title {
    margin: 10px 10px;
  }
  .prop-title a{
    font-size: 14px;
    line-height: 18px;
    font-family: "Roboto", Helvetica, Arial, sans-serif;
    font-weight: normal;
    color: $label_color;
    &:hover{
      color: $linkColorHover;
    }
  }
  p {
    margin: 10px;
  }
  span.price{
    font-size: 11px;
    line-height: 18px;
    color: $linkColorHover;
    font-family: "Roboto", Helvetica, Arial, sans-serif;
  }
}


/*
 *  Floor Plans
 */
.floor-plans{
  padding: 25px;
  position: relative;
  border-bottom: 1px solid $border_color_two;

  img {
    margin-bottom: 0;
  }

  .floor-plans-label {
    @extend  .common-label;
    line-height: 20px;
    margin: 0 0 10px;
  }

  .floor-plans-accordions{
    // border: 1px solid $border_color_two;
  }

  .floor-plan{
    margin-bottom: 10px;
    &:last-child{
      margin-bottom: 0;
    }
  }

  .floor-plan-title{
    padding: 10px 15px;
    border: 1px solid $border_color_two;
    cursor: pointer;
    background-color: #f5f5f5;
    color: $headingsColor;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    .fa{
      font-size: 14px;
      line-height: 22px;
      float: left;
      margin-right: 10px;
    }
    h3{
      float: left;
      margin: 0;
      font-size: 14px;
      font-weight: normal;
      line-height: 22px;
      -webkit-user-select: text;
      -moz-user-select: text;
      -ms-user-select: text;
      user-select: text;
    }
    &:hover{
      background-color: darken( #f5f5f5, 2% );
    }
  }

  .floor-plan.current{
    .floor-plan-title{
      background-color: darken( #f5f5f5, 2% );
    }
  }

  .floor-plan-meta {
    float: right;
    margin: 0;
    padding: 0 ;
    width: auto;
    overflow:hidden;
    font-size: 12px;
    div{
      display: inline-block;
      margin: 0 8px;
      -webkit-user-select: text;
      -moz-user-select: text;
      -ms-user-select: text;
      user-select: text;
      span{
        margin-right: 4px;
      }
      &:first-child{
        margin-left: 0;
      }
      &:last-child{
        margin-right: 0;
      }
      .floor-price-value{
        color: #ec894d;
      }
    }
  }

  .floor-plan-content{
    padding: 15px 20px 20px;
    border: 1px solid $border_color_two;
    border-top: none;
    background-color: #fff;
    display: none;
  }

  .floor-plan-desc{
    margin: 0 0 20px;
  }

  .floor-plan-map{
    margin: 0;
  }

}

/* Properties list layout shortcode in floor plans */
.detail #overview .floor-plan-desc .inspiry-shortcode {
  .property-item {
    figure {
      width: 234px;
    }
    .detail {
      width: 340px;
    }
  }
}

/* Property grid layout shortcode in floor plans */
.detail #overview .floor-plan-desc .inspiry-shortcode.property-grid {
  .list-container {
    margin-left: -12px;
  }
  .property-item {
    margin-left: 10px;
  }
}


/* Agent contact details */
.contacts-list{
  list-style: none;
  margin: 0;
  color: $headingsColor;
  li{
    border-bottom: 1px solid $border_color_two;
    line-height: 16px;
    padding: 10px 0;
    svg {
      margin-right: 5px;
      vertical-align: top;
      width: 16px;
      height: auto;
      .path,
      .circle{
        fill: $headingsColor;
      }
    }
  }
}


.page-carousel .featured-properties-carousel{
  border: 1px solid $border_color_two;
  background: $white;
  padding:  16px 0 15px;
  margin-bottom: 80px;
  .narrative {
    padding-bottom: 13px;
  }
  .carousel {
    margin-bottom: 4px;
  }

  .es-carousel-wrapper ul li .price {
    background:  none;
    color: #37b3d9;
    font-size: 14px;
  }

}

/*	Password Protected Form on Property Detail Page
--------------------------------------------------------*/
.single-property form.post-password-form {
  padding: 50px;
  border-top: 1px solid #dedede;
  border-bottom: 1px solid #dedede;
  input[type=submit]{
    background-color: #ec894d;
    border: none;
    color: #fff;
    padding: 5px 10px;
    &:hover {
      background-color: #e3712c ;
    }
  }
}


/*	Similar Properties on Property Detail Page
--------------------------------------------------------*/
.detail{
  .listing-layout{
    border: 1px solid #dedede;
  }

  .property-grid {
    .property-item {
      min-height: initial;
    }
  }

  #overview .inspiry-shortcode{
    .list-container{
      padding: 0;
    }
    .property-item{
      border: 1px solid $border_color_two;
      padding: 10px 10px 0;
      figure{
        width: 224px;
      }
      .detail{
        width: 394px;
      }
      .price{
        background: #4dc7ec;
        float: none;
        margin: 0 -11px 10px -18px;
        padding: 8px 20px 10px 13px;
      }
      .property-meta{
        border: none;
        border-top: 1px solid $border_color_two;
        margin: 0 -10px;
      }
    }
    &.property-grid .property-item{
      border: none;
      padding: 0;
      margin: 0 0 0 30px;
      min-height: 310px;
      figure{
        width: auto;
      }
    }
  }

  .list-container {
    padding: 10px 24px 24px;
    h3{
      font-family: $RobotoFontFamily;
      font-size: 20px;
      font-weight: normal;
      margin: 0 0 7px 30px;
      text-transform: capitalize;
    }
  }

}

/* slider socket */
.slider-main-wrapper {
  position: relative;

  div + .slider-socket {
    position: absolute;

    &.thumb-on-bottom {
      left: 19px;
      top: 20px;
    }
  }

  .slider-socket {

    padding: 7px 10px 5px;
    top: 0;
    left: 0;

    span {
      display: inline-block;
      vertical-align: top;

      form {
        display: none;
      }

      svg {
        width: 20px;
      }
    }

    .printer-icon {
      font-size: 20px;
      margin-left: 10px;

      i {
        color: #fff;
      }

      &:hover {
        i {
          color: rgb(41, 45, 45);
        }
      }
    }
  }
}

/*  property detail slider variation 2
--------------------------------------------------------*/
#property-slider-two-wrapper{
  padding: 19px 19px 3px;
  background-color: #f5f5f5;
  border-top: 1px solid #dedede;
  .flexslider {
    border: none;
    margin: auto;
    border-radius: 0;
  }
}
#property-slider-two{
  min-height: 320px;
  .flex-direction-nav{
    a{
      display: block;
      text-indent: -999em;
      text-align: left;
      width: 32px;
      height: 32px;
      margin-top: -16px;
      z-index: 150;
      cursor: pointer;
      transition: none;
      text-shadow: none;
      outline: none;
      &:focus{
        outline: none;
      }
      &::before{
        display: none;
      }
    }
    a.flex-prev{
      background: url(../../images/prop-slider-nav-left.png) no-repeat 0px 0px;
      left: 20px;
      &:hover{
        background-position: bottom;
      }
    }
    a.flex-next{
      background: url(../../images/prop-slider-nav-right.png) no-repeat 0px 0px;
      right: 20px;
      &:hover{
        background-position: bottom;
      }
    }
    .flex-disabled{
      opacity: 0;
      filter: alpha( opacity=0 );
      cursor: default;
    }
  }
  .flexslider.loading{
    background: transparent url('../../images/slider-loader.gif') no-repeat center center;
  }
}
#property-carousel-two{
  padding: 0 28px;
  .slides li{
    margin: 10px 10px 10px 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    img{
      cursor: pointer;
      opacity: 0.7;
      filter: alpha(opacity=70);
    }
    &:hover,
    &.flex-active-slide{
      img{
        opacity: 1;
        filter: alpha(opacity=100);
      }
    }
  }
  .flex-direction-nav{
    a{
      opacity: 1;
      background-color: #4dc7ec;
      background-image: url("../../images/arrow-right-white.png");
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 8px 11px;
      height: 40px;
      width: 40px;
      text-indent: -999em;
      text-align: left;
      transition: none;
      text-shadow: none;
      outline: none;
      &:hover{
        background-color: darken( #4dc7ec, 10%);
      }
      &:focus{
        outline: none;
      }
      &::before{
        display: none;
      }
    }
    a.flex-prev{
      background-image: url("../../images/arrow-left-white.png");
      left: 0;
    }
    a.flex-next{
      background-image: url("../../images/arrow-right-white.png");
      right: 0;
    }
    .flex-disabled{
      opacity: 1!important;
      filter:alpha(opacity=100);
      cursor: default;
      z-index: 100;
      pointer-events: none;
    }
  }
}

